.reviews{
  &__top{
    text-align: center;
  }
  &__tabs{
    display: flex;
    justify-content: center;
    margin-top: 2.75em;
  }
  &__tab{
    width: 4.375em;
    height: 4.375em;
    flex-shrink: 0;
    border-radius: 50%;
    margin: 0 1.125em;
    position: relative;
    cursor: pointer;
    &._active{
      &:before{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      @include centerAbsolute;
      border-radius: 50%;
      transition: all .3s;
      width: 5.75em;
      height: 5.75em;
      border: 2px solid $primary;
      opacity: 0;
      pointer-events: none;
    }
    img{
      width: 100%;
      height: 100%;
      display: block;
      border-radius: inherit;
    }
  }
  .tab-content{
    margin-top: 3.45em;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.review-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 57.625em;
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
  &:before{
    content: '';
    width: 3.75em;
    height: 2.63em;
    background-image: url(../img/quote.svg);
    @include bgImgSize(100%);
    margin-bottom: 2.5em;
  }
  &__info{
    margin-top: 2.45em;
  }
}

.offer{
  padding: 6.75em 0 9em;
  border-radius: 0.625em;
  background-color: $secondary;
  &__inner{
    display: grid;
    grid-template-columns: 40.5em calc(100% - 40.5em - 8.875em);
    justify-content: space-between;
  }
  &__media{
    display: grid;
    grid-template-columns: 45.2% calc(54.8% - .875em);
    gap: .875em;
    align-items: flex-end;
    position: relative;
  }
  &__img{
    @include imgCover(133%);
    border-radius: 3.75em 0.25em 0.25em 0.25em;
    &._type2{
      border-radius: 0.25em 3.75em 0.25em 0.25em;
      padding-bottom: 157%;
    }
  }
  .main-advantage{
    &:not(._type2){
      top: 6.94em;
      left: 7.81em;
      bottom: unset;
      right: unset;
    }
    &._type2{
      right: -6.25em;
      bottom: 4.12em;
    }
  }
  &__content{
    color: #fff;
  }
  &__title{
    max-width: 14em;
  }
  &__desc{
    margin-top: 1.69em;
  }
  &__list{
    margin-top: 2em;
    @include grid100gap(1.25em);
    &-item{
      display: flex;
      &:before{
        content: '';
        flex-shrink: 0;
        width: 1em;
        height: 1em;
        background-image: url(../img/check.svg);
        @include bgImgSize(100%);
        margin-right: 1em;
        margin-top: .375em;
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.25em;
  }
}

.platform-workers{
  position: absolute;
  left: 3em;
  bottom: 0;
  transform: translateY(50%);
  padding: .9375em 1.5em;
  border-radius: 1em;
  background-color: #fff;
  box-shadow: $shadow2;
  display: flex;
  align-items: center;
  .text15{
    transform: translateY(5%);
  }
  &__items{
    display: flex;
    margin-left: 1.44em;
  }
  &__item{
    width: 2.5em;
    height: 2.5em;
    &:not(:first-child){
      margin-left: -.625em;
    }
    img{
      border: .125em solid #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .reviews{
    padding: 1.94em 0 4.43em;
    &__tabs{
      margin-top: 2.3em;
      justify-content: space-between;
    }
    &__tab{
      width: 3.13em;
      height: 3.13em;
      margin: 0;
      &:before{
        width: 4.12em;
        height: 4.12em;
        border-width: 1px;
      }
    }
    @media screen and (min-width: 577px){
      &__tabs{
        justify-content: center;
      }
      &__tab{
        margin: 0 .75em;
      }
    }
    .tab-content{
      margin-top: 1.44em;
    }
  }

  .review-card{
    &:before{
      width: 2.875em;
      height: 2.01563em;
      margin-bottom: 1.81em;
    }
    &__text{
      font-size: 1.125em;
      line-height: 155%;
    }
    &__info{
      margin-top: 1.81em;
      @include grid100gap(.375em);
    }
    &__name,
    &__co{
      font-size: .875em;
    }
  }

  .offer{
    padding: 1.125em 0 2.5em;
    border-radius: 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.56em;
    }
    .platform-workers,
    .main-advantage{
      display: none;
    }
    &__title{
      max-width: 100%;
      text-align: center;
    }
    &__desc{
      text-align: center;
      margin-top: 1.38em;
      .text15{
        line-height: 171%;
      }
    }
    &__list{
      margin-top: 1.375em;
      gap: .625em;
      &-item{
        .text15{
          line-height: 187%;
        }
      }
    }
    &__nav{
      margin-top: 1.81em;
      justify-content: center;
    }
  }
}


@media screen and (max-width: 576px){
  .offer{
    &__media{
      grid-template-columns: 45.2% calc(54.8% - .5em);
      gap: .5em;
    }
    &__img{
      border-radius: 2.79675em 0.18644em 0.18644em 0.18644em;
      &._type2{
        border-radius: 0.18644em 2.79675em 0.18644em 0.18644em;
      }
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 427px){
  .reviews{
    padding: 2.5em 0 3.3em;
    .tab-content{
      margin-top: 1.44em;
    }
  }

  .review-card{
    &:before{
      margin-bottom: 1.44em;
    }
    &__info{
      margin: 1.44em auto 0;
      gap: .375em;
      max-width: 12em;
    }
  }

  .offer{
    padding: 1.5em 0;
  }
}
