@import "_header";
@import "_footer";

.admin-page{
  @media screen and (min-width: 993px){
    font-size: calc(1920px * (16 / 1920));
    @media screen and (max-width: 1920px){
      font-size: calc(100vw * (16 / 1920));
    }
  }
  .container{
    padding: 0 1.875em;
    max-width: 100%;
  }
  .wrapper{
    padding: 5.625em 1.875em 0 18.75em;
  }
  .content{
    border-radius: 0.625em;
    background: rgba(243, 244, 255, 0.42);
    padding: 2.5em 3.75em;
  }
  .pagination{
    margin-top: auto;
    padding-top: 1.875em;
  }
}

.footer2{
  padding: 1.875em 0 2.9375em;
}

.admin-header{
  padding: 1.25em 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  background-color: #fff;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 9.3125em;
    flex-shrink: 0;
    margin-right: 11.17em;
    img{
      width: 100%;
      display: block;
    }
  }
  .blog-field{
    width: 37.9375em;
    input{
      font-size: .875em;
    }
  }
  &__type{
    margin-right: 2.875em;
    display: flex;
    align-items: center;
    color: #000;
    min-width: 9em;
    .m-switch{
      display: flex;
      align-items: center;
      label{
        font-size: .7em;
        margin-right: 1.25em;
        background-color: $primary;
      }
      ._view2{
        display: none;
      }
      input:checked{
        &~label{
          background-color: $secondary;
        }
        &~.text14{
          ._view1{
            display: none;
          }
          ._view2{
            display: block;
          }
        }
      }
    }
    .text14{
      transform: translateY(5%);
    }
  }
  .header-action2{
    display: flex;
    align-items: center;
    margin-left: auto;
    .burger{
      display: none;
    }
    @media screen and (min-width: 993px){
      &__btn{
        margin-right: 1.13em;
      }
      &__user{
        margin-left: 1.06em;
      }
      .avatar{
        width: 3.125em;
        height: 3.125em;
      }
    }
  }

}

.admin-menu{
  @media screen and (min-width: 993px){
    position: fixed;
    z-index: 19;
    left: 0;
    top: 0;
    height: $vh;
    width: 18.75em;
    padding-top: 6em;
    padding-bottom: 4em;
    overflow-y: auto;
    .mob-menu{
      &__link{
        min-height: 3.4375em;
        padding: .625em 1.875em;
        svg{
          width: 1.5em;
          margin-right: .875em;
        }
      }
      &__num{
        height: 1.25em;
        min-width: 1.25em;
        span{
          font-size: .625em;
        }
      }
      &__title{
        font-size: 1.0625em;
      }
      &__account{
        &-top{
          padding: 0 1.875em;
          .text14{
            font-size: .9375em;
            line-height: 367%;
          }
        }
      }
    }
  }
}

.m-switch{
  label{
    display: flex;
    align-items: center;
    width: 3.375em;
    height: 1.875em;
    background-color: rgba($primary,.1);
    border-radius: 2.1875em;
    padding: .1875em;
    transition: all .3s;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1.5em;
      height: 1.5em;
      border-radius: 50%;
      transition: inherit;
      box-shadow: 0 .125em .125em rgba(17, 33, 55, 0.10);
      background-color: #fff;
    }
  }
  input:checked{
    &~label{
      background-color: $primary;
      &:before{
        transform: translateX(100%);
      }
    }
  }
}

.footer2{
  color: rgba($gray,.7);
  .footer-bottom{
    padding: 0;
    border: none;
  }
  .footer-bottom__c{
    color: rgba($gray,.7);
  }
  .footer-dropdown{
    &__toggle{
      color: rgba($gray,.7);
      border: $border;
      path{
        fill: rgba($gray,.7);
      }
    }
    &__list{
      box-shadow: $shadow;
      border: $border;
      background-color: #fff;
      a{
        color: $black;
        &._active{
          background-color: $primaryWhite;
        }
      }
    }
  }
}

.admin-top{
  margin-bottom: 1.875em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__btn{
    height: 3.75em;
    padding: 0 2.19em;
  }
}


@media screen and (max-width: 992px){
  .admin-page{
    .container{
      padding: 0 .9375em;
    }
    .wrapper{
      padding:0;
    }
    .content{
      padding: 1em 1em 1.38em;
      border-radius: 0;
    }
    .pagination{
      margin-top: 1.375em;
    }
  }

  .admin-header{
    position: relative;
    padding: 1em 0;
    .blog-field,
    &__type{
      display: none;
    }
    &__logo{
      margin-right: 0;
      position: relative;
      z-index: 21;
    }
    .header-action2{
      &__btn{
        display: flex;
      }
      ._favourite{
        display: none;
      }
      .burger{
        display: flex;
      }
      &__user{
        display: block;
      }
    }

  }

  .footer2{
    padding: 1em 1em 1.94em;
  }

  .admin-top{
    margin-bottom: .69em;
  }
}


@media screen and (max-width: 576px){
  .admin-header{
    &__logo{
      width: 6.8125em;
    }
  }

  .footer2{
    .footer-bottom__c{
      text-align: center;
    }
    .footer-bottom__nav{
      padding: 0;
      justify-content: center;
      margin-top: .25em;
    }
  }

  .admin-top{
    flex-direction: column;
    align-items: flex-start;
    &__btn{
      height: 3em;
      width: 100%;
      margin: .5em 0 0;
    }
  }
}
