.m-section{
  padding: 7.5em 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 3.75em;
  }
  &__link{
    height: 3em;
    margin-left: 1em;
    padding: 0 1.875em;
    flex-shrink: 0;
    border-width: 2px;
    img{
      width: 1.25em;
      margin-left: .625em;
    }
  }
}

.swiper-nav{
  @include center;
  margin-top: 1.5em;
  .swiper-button{
    margin: 0 .625em;
    position: static;
    width: 1.5em;
    height: auto;
    @include center;
    svg{
      width: 100%;
    }
  }
  .swiper-pagination{
    position: static;
    width: auto;
  }
}

.swiper-pagination{
  @include center;
  &-bullet{
    width: 0.5em;
    height: 0.5em;
    margin: 0 .375em !important;
    background-color: #DBDDFB;
    opacity: 1;
    &-active{
      background-color: $primary;
    }
  }
}

.categories-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 1.75em * .75));
        gap: 1.75em;
      }
      &-slide{
        width: 100%;
        height: 100%;
      }
      &-nav{
        display: none;
      }
    }
  }
}

.category-card{
  padding: 2.5em 1.5em 2.5em 1.875em;
  border-radius: 0.25em;
  display: block;
  &:hover{
    color: $black;
    box-shadow: $shadow;
  }
  &__icon{
    height: 3.25em;
    flex-shrink: 0;
    img{
      height: 100%;
      display: block;
    }
  }
  &__count{
    margin-top: 1.75em;
  }
  &__info{
    margin-top: .31em;
    @include grid100gap(.56em);
  }
}

.services{
  overflow: hidden;
  background-color: $primaryWhite;
}

.product-slider{
  position: relative;
  &__container{
    width: calc(100% + 1.75em);
  }
  &__wrapper{
    align-items: stretch;
  }
  &__slide{
    width: 25%;
    padding-right: 1.75em;
    height: auto;
  }
  &__nav{
    .swiper-button{
      ._view2{
        display: none;
      }
    }
    @media screen and (min-width: 993px){
      .swiper-pagination{
        display: none;
      }
      .swiper-button{
        box-shadow: 0 .625em 2.1875em rgba(5, 16, 54, 0.10);
        width: 3.75em;
        height: 3.75em;
        background-color: #fff;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
        svg{
          width: 1.0625em;
        }
        &:hover{
          background: #E3E6FF;
          border-color: $primary;
        }
        &-prev{
          left: -1.875em;
        }
        &-next{
          right: -1.875em;
        }
      }
    }

  }
}

.service-card{
  border-radius: 1.25em;
  border: $border;
  background: #FFF;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  &__slider{
    position: relative;
    margin: -1px 0 0 -1px;
    width: calc(100% + 2px);
    &:hover{
      .swiper-nav{
        opacity: 1;
      }
    }
    .swiper{
      &-container{
        overflow: hidden;
      }
      &-nav{
        position: absolute;
        width: 100%;
        left: 0;
        bottom: .625em;
        opacity: 0;
        transition: all .1s;
      }
      &-pagination{
        &-bullet{
          width: 0.375em;
          height: 0.375em;
          background-color: rgba(#fff,.6);
          margin: 0 .25em !important;
          &-active{
            width: .5em;
            height: .5em;
            background-color: #fff;
          }
        }
      }
      &-button{
        width: 1.25em;
        margin: 0 1em;
        path{
          stroke: #fff;
        }
      }
    }
  }
  &__img{
    @include imgCover(74.5%);
  }
  &__content{
    padding: 1.25em 1.875em 1.125em;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__type{
    margin-bottom: .25em;
    color: $gray;
  }
  &__title{
    @include truncate(2);
    &:hover{
      text-decoration: underline;
    }
  }
  &__reviews{
    margin: .625em 0;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.0625em;
    border-top: $border;
  }
  &__reviews{
    img{
      width: .625em;
      margin-right: .625em;
      transform: translateY(-.125em);
    }
  }
  &__user{
    .avatar{
      position: relative;
      margin-right: .625em;
    }
    &-online{
      position: absolute;
      top: .0625em;
      right: -.125em;
      width: 0.5625em;
      height: 0.5625em;
      border-radius: 50%;
      display: block;
      border: .0625em solid #fff;
      background-color: $green;
    }
  }
  &__price{
    margin-left: auto;
  }
  .favourite-btn{
    position: absolute;
    top: 1.38em;
    right: 1.38em;
    z-index: 3;
  }
}

.avatar{
  display: block;
  width: 1.875em;
  height: 1.875em;
  border-radius: 50%;
  flex-shrink: 0;
  img{
    border-radius: inherit;
    width: 100%;
    height: 100%;
    display: block;
  }
}

.favourite-btn{
  width: 2.25em;
  height: 2.25em;
  border-radius: 50%;
  @include center;
  border: $border;
  box-shadow: 0 .375em .9375em rgba(64, 79, 104, 0.05);
  svg{
    width: 1em;
  }
  &._added{
    path{
      fill: red;
      stroke: red;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    &__top{
      flex-direction: column;
      margin-bottom: 1.5em;
    }
    &__text{
      @include grid100gap(.625em);
      text-align: center;
    }
    &__link{
      margin: .625em auto 0;
      height: auto;
      border: none;
      padding: 0;
      &:hover{
        background-color: transparent;
        opacity: .9;
      }
    }
  }

  .categories{
    padding: 1.5em 0 2.7em;
    overflow: hidden;
  }

  .categories-slider{
    .swiper{
      &-slide{
        width: 21.875em;
        padding-right: 1.25em;
      }
    }
  }

  .category-card{
    box-shadow: 0 .375em .9375em rgba(64, 79, 104, 0.05);
  }

  .services{
    padding: 1.75em 0 3.125em;
  }

  .product-slider {
    &__container {
      width: 100%;
    }
    &__slide {
      width: 21.25em;
      padding-right: .625em;
    }
  }
}


@media screen and (max-width: 427px){
  .categories-slider{
    .swiper{
      &-slide{
        width: 16.25em;
        padding-right: .8125em;
      }
    }
  }

  .category-card{
    padding: 1.25em;
    &__count{
      .text15{
        font-size: .875em;
        line-height: 200%;
      }
    }
    &__info{
      margin-top: .44em;
      gap: .44em;
      .text20{
        font-size: 1.125em;
      }
      .text14{
        line-height: 157%;
      }
    }
  }

  .services{
    padding: 1.38em 0 1em;
  }

  .product-slider {
    &__slide {
      width: 16.5em;
      padding-right: 1em;
    }
    .favourite-btn{
      width: 1.875em;
      height: 1.875em;
      top: .875em;
      right: .875em;
      svg{
        width: .875em;
      }
    }
  }

  .service-card{
    border-radius: .9375em;
    &__content{
      padding: 1em 1.125em;
    }
    &__type{
      .text14{
        font-size: .75em;
      }
    }
    &__title{
      font-size: .9375em;
    }
    &__reviews{
      .text15{
        font-size: .8125em;
      }
      .text14{
        font-size: .75em;
      }
    }
    &__bottom{
      min-height: 2.5em;
    }
    &__user{
      padding-right: .25em;
      .avatar{
        width: 1.3125em;
        height: 1.3125em;
        margin-right: .56em;
      }
      &-online{
        width: .5em;
        height: .5em;
        top: 0;
        right: -.0625em;
      }
      .text14{
        font-size: .75em;
      }
      &-name{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    &__price{
      white-space: nowrap;
      .text14{
        font-size: .75em;
      }
      .text17{
        font-size: .875em;
      }
    }
  }
}
