.finance-table,
.disputes-table{
  td{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    border-bottom: $border;
  }
}

.finance-table{
  th,td{
    &:nth-child(1){
      width: 11.875em;
    }
  }
}

.finance-row{
  &__title{
    display: none;
  }
  &__desc{
    font-size: .9375em;
    line-height: 186%;
    transform: translateY(5%);
  }
  &__type{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-right: auto;
    height: 2.125em;
    width: 6.375em;
    border-radius: 0.375em;
    border: 1px solid #5D68F1;
    color: $primary;
    span{
      font-size: .8125em;
      line-height: 120%;
      transform: translateY(5%);
    }
    &._type2{
      background-color: $primaryWhite;
      border-color: $primaryWhite;
    }
  }
  &__user{
    display: flex;
    align-items: center;
    .avatar{
      width: 1.875em;
      height: 1.875em;
      margin-right: .875em;
    }
  }
  &__messages{
    display: flex;
    align-items: center;
    &._disabled{
      color: #A7A7A7;
       .finance-row__count{
         background-color: #D9D9D9;
       }
    }
  }
  &__count{
    padding: 0 .2em;
    min-width: 1.375em;
    height: 1.375em;
    background-color: #FF6262;
    color: #fff;
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: .375em;
    @include center;
    font-weight: 700;
    span{
      font-size: .6875em;
      line-height: 130%;
      transform: translateY(5%);
    }
  }
}

.finance-stat{
  margin-bottom: 1.875em;
}

.stat-card3{
  padding: 1.19em 1.875em;
  border-radius: 0.4375em;
  background: #FFF;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__content{
    @include grid100gap(.375em);
  }
  &__title{
    font-size: .9375em;
    line-height: 186%;
    color: $gray;
  }
  &__num{
    font-size: 1.75em;
    line-height: 143%;
    font-weight: 500;
  }
  &__desc{
    font-size: .875em;
    line-height: 200%;
    span{
      color: $primary;
    }
  }
  &__img{
    margin-left: 1em;
    img{
      max-width: 4.0625em;
      max-height: 3.8125em;
    }
  }
}

.disputes-table{
  td{
    &:last-child{
      width: 17em;
    }
  }
}


@media screen and (max-width: 992px){
  .disputes-table,
  .finance-table{
    padding: 0;
    background-color: transparent;
    tbody{
      display: grid;
      gap: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
    tr{
      padding: 1.19em 1.56em;
      border-radius: 0.4375em;
      background: #FFF;
      @include grid100gap(.875em);
    }
    td{
      padding: 0;
      width: 100% !important;
      border: none;
      @include grid100gap(.25em);
    }
  }

  .disputes-table{
    tr{
      padding-top: .84em;
      padding-bottom: .84em;
    }
  }

  .finance-row{
    &__title{
      display: block;
      font-size: .75em;
      line-height: 180%;
      font-weight: 500;
    }
    &__desc{
      font-size: .875em;
      line-height: 157%;
    }
    &__user{
      .avatar{
        width: 2.4375em;
        height: 2.4375em;
      }
      .finance-row__desc{
        font-size: .8125em;
        font-weight: 400;
      }
    }
    &__messages{
      padding: .625em 0;
    }
  }

  .finance-stat{
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1em;
  }

  .stat-card3{
    padding: .9375em .9375em 1.81em;
    flex-direction: column;
    align-items: flex-start;
    justify-content: unset;
    &__img{
      margin: 0 0 .54em;
      order: -1;
      img{
        height: 2.53em;
        max-height: unset;
        max-width: unset;
      }
    }
    &__content{
      gap: 0;
    }
    &__title{
      font-size: .75em;
      line-height: 167%;
      margin-bottom: 2em;
    }
    &__num{
      font-size: 1.375em;
    }
    &__desc{
      font-size: .75em;
      line-height: 167%;
    }
  }

  .disputes-table{
    td{
      &:nth-child(3){
        order: -1;
        padding: .625em 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .disputes-table,
  .finance-table{
    tbody{
      grid-template-columns: 100%;
    }
  }

  .finance-stat{
    grid-template-columns: repeat(2,calc(50% - .5em));
  }
}
