.advantages-seller{
  .advantages__top{
    text-align: left;
  }
}

.faq{
  padding: 3.06em 0 6.125em;
  &__top{
    text-align: center;
    margin-bottom: 3.75em;
  }
  &__items{
    width: 57.75em;
    max-width: 100%;
    margin: 0 auto;
    @include grid100gap(.5em);
  }
}

.qa-card{
  padding: 2.5em 2.5em 0;
  border-radius: 0.4375em;
  transition: all .3s;
  &._active{
    background-color: $primaryWhite;
    ._hide{
      opacity: 0;
    }
    .qa-card__top svg{
      width: 1.31em;
    }
  }
  &__top{
    transition: all .3s;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    svg{
      width: 1em;
      margin-left: 1.5em;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 500;
  }
  &__btn{
    height: 1.75em;
    @include center;
  }
  .spoiler__wrap{
    padding: .81em 0 2.125em;
  }
}

.seller{
  padding: 3.75em 0 0;
  &__grid{
    display: grid;
    grid-template-columns: 27.5em calc(100% - 29em);
    gap: 1.5em;
  }
  &__tabs{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__tab{
    padding: .31em 0;
    color: $gray;
    border-bottom: 2px solid transparent;
    transition: all .3s;
    @include lastM(0 0 1em);
    &:hover{
      color: $black;
      cursor: pointer;
    }
    span{
      font-size: 1.0625em;
      line-height: 165%;
    }
    &._active{
      font-weight: 500;
      color: $black;
      border-color: $black;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .faq{
    padding: 2em 0 3.25em;
    &__top{
      text-align: left;
      margin-bottom: 1em;
      .text15{
        display: none;
      }
    }
  }

  .qa-card{
    padding: .81em 1.67em 0;
    .spoiler__wrap{
      padding: .5em 0 1.31em;
    }
    &__title{
      font-size: .9375em;
      line-height: 160%;
    }
    &__btn{
      height: 1.5em;
    }
  }

  .seller{
    padding: 1.12em 0 .5em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__tabs{
      flex-direction: row;
    }
    &__tab{
      @include lastM(0 2.19em 0 0);
    }
    &__nav{
      margin-top: 1.25em;
      .m-btn{
        width: 100%;
        height: 3.75em;
      }
    }
  }
}


@media screen and (max-width: 427px){
  .faq{
    padding: 1.625em 0 2.44em;
  }

  .qa-card{
    padding: .81em 1.25em 0;
  }

  .seller{
    padding: .9375em 0 .5em;
    &__grid{
      gap: 1.81em;
    }
    &__tab{
      @include lastM(0 2.06em 0 0);
    }
    &__nav{
      margin-top: 1.875em;
    }
  }
}
