.auth{
  padding: 2.25em 0 7.5em;
  &__top{
    text-align: center;
    @include grid100gap(.44em);
  }
}

.auth-form{
  width: 42.875em;
  max-width: 100%;
  margin: 2.5em auto 0;
  border-radius: 0.5em;
  border: $border;
  box-shadow: $shadow;
  padding: 2.19em 3.125em 2.81em;
  &__top{
    @include grid100gap(.5em);
  }
  &__desc{
    a{
      text-decoration: underline;
      color: $primary;
      font-weight: 500;
      &:hover{
        text-decoration: none;
      }
    }
  }
  &__fields{
    margin-top: 1.69em;
    @include grid100gap(1.25em);
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .m-check{
      span{
        font-size: 1em;
        line-height: 175%;
      }
    }
  }
  &__link{
    font-size: 1em;
    line-height: 175%;
  }
  &__link2{
    display: none;
  }
}

.social-auth{
  margin-top: 1.625em;
  &__top{
    @include center;
    position: relative;
    color: $dark;
    font-weight: 300;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-color: #EAEAEA;
      width: 100%;
      height: 1px;
    }
    .text15{
      padding: 0 1em;
      background-color: #fff;
      position: relative;
      z-index: 2;
    }
  }
  &__links{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.88em / 3));
    gap: 1.44em;
    margin-top: 1.31em;
  }
  &__link{
    padding: 0 1.625em;
    border-radius: 0.5em;
    border: 1px solid #1A9FFF;
    color: #1A9FFF;
    svg{
      width: 1.0625em;
      margin-right: 1.25em;
    }
    span{
      font-size: .875em;
    }
    &:before{
      background-color: rgba(#1A9FFF,.1);
    }
    &:hover{
      color: #1A9FFF;
    }
    &._google{
      color: #D93025;
      border-color: #D93025;
      &:hover{
        color: #D93025;
      }
      &:before{
        background-color: rgba(#D93025,.1);
      }
    }
    &._vk{
      color: #1E88E5;
      border-color: #1E88E5;
      &:hover{
        color: #1E88E5;
      }
      &:before{
        background-color: rgba(#1E88E5,.1);
      }
    }
  }
}



@media screen and (max-width: 992px){
  .auth{
    padding: 0 0 2.63em;
  }

  .auth-form{
    margin-top: 3.31em;
    padding: 1.25em 1.75em 1.56em;
    &__top{
      text-align: center;
    }
    &__title{
      font-size: 1.125em;
      line-height: 135%;
    }
    &__desc{
      font-size: .875em;
      line-height: 171%;
    }
    &__fields{
      gap: .81em;
      margin-top: 1.5em;
    }
    &__wrap{
      display: block;
      .m-check{
        span{
          font-size: .875em;
          line-height: 200%;
        }
      }
    }
    &__link{
      display: none;
    }
    &__link2{
      display: inline-block;
      margin: -.44em auto 0;
      font-size: .75em;
      line-height: 233%;
    }
  }

  .social-auth{
    margin-top: .5em;
    &__top{
      .text15{
        font-size: .625em;
        line-height: 280%;
        padding: 0 2.8em;
      }
    }
    &__links{
      margin-top: .5em;
      display: flex;
      gap: unset;
      justify-content: center;
    }
    &__link{
      padding: 0;
      width: 2.875em;
      height: 2.875em;
      margin: 0 .75em;
      svg{
        margin: 0;
      }
      span{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 427px){
  .auth-form{
    margin-top: 1.06em;
    padding: .81em 1.37em 1.56em;
    &__title{
      font-size: 1em;
      line-height: 143%;
    }
  }

  .social-auth{
    &__link{
      margin: 0 .3125em;
    }
  }
}
