.profile-item{
  padding: 1.875em;
  background-color: #fff;
  border-radius: .4375em;
  @include lastM(0 0 1.6875em);
  &__top{
    padding: 0 0 1.25em;
    border-bottom: $border;
    .text17{
      transform: translateY(5%);
    }
  }
  &__bottom{
    padding-top: 1.875em;
    @media screen and (min-width: 993px){
      &._pt0{
        padding-top: 0;
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.25em 1.875em;
    margin-top: 1.875em;
    max-width: 58em;
    &._type2{
      @media screen and (min-width: 993px){
        grid-template-columns: 100%;
        .fg{
          max-width: calc(50% - .9375em);
        }
      }
    }
    &:first-child{
      margin-top: 0;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__btn{
    width: 13.375em;
    height: 3.75em;
  }
  &__ask{
    @include grid100gap(.25em);
  }
}

.photo-field{
  display: flex;
  align-items: center;
  .avatar{
    width: 5.625em;
    height: 5.625em;
    margin-right: 1.3125em;
  }
  &__action{
    display: flex;
  }
  &__btn{
    padding: 0 1.25em;
    height: 2.5em;
    border-radius: 0.25em;
    background-color: $primaryWhite;
    @include lastM(0 .625em 0 0);
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    svg{
      width: 1.375em;
    }
  }
  &__delete{
    padding: 0;
    width: 2.5em;
  }
  &__desc{
    margin-top: .625em;
  }
}

.project-files{
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: -1em;
  }
  &__hint{
    margin-top: 1.25em;
    font-weight: 300;
    color: $gray;
  }
}

.file-item,
.file-input{
  width: 13.125em;
  margin: 1em 1.75em 0 0;
}

.file-item{
  padding: 1em 1em 1em 1.25em;
  border-radius: 0.25em;
  background-color: $primaryWhite;
  background-image: url(../img/file.svg);
  background-repeat: no-repeat;
  background-position: right 1.38em bottom 0;
  background-size: 3.375em auto;
  &__title{
    font-size: .875em;
    line-height: 200%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  &__bottom{
    display: flex;
    margin-top: -.25em;
  }
  &__weight,
  &__btn,
  &__format{
    color: $primary;
    font-size: 0.875em;
    font-weight: 500;
    line-height: 200%;
  }
  &__weight{
    color: #BABFE4;
    margin-left: 1.21em;
  }
}

.file-input{
  height: 5.5em;
  min-height: 100%;
  &:hover{
    opacity: .8;
  }
  &__wrap{
    @include center;
    width: 100%;
    height: 100%;
    border-radius: 0.5em;
    border: 1px dashed $primary;
  }
  &__title{
    font-size: .9375em;
    line-height: 185%;
    font-weight: 500;
    color: $primary;
    transform: translateY(5%);
  }
}


@media screen and (max-width: 992px){
  .profile-item{
    padding: 0;
    @include lastM(0 0 1.25em);
    &__top{
      padding: .69em 1.06em .75em;
      border-bottom: $border;
      .text17{
        font-size: 1em;
        line-height: 175%;
      }
    }
    &__bottom{
      padding: 1.125em 1.06em .94em;
    }
    &__grid{
      margin-top: 2em;
      grid-template-columns: 100%;
      gap: 1.125em;
    }
    &__nav{
      margin-top: .81em;
      .m-btn{
        width: 100%;
        height: 3.125em;
        padding: 0;
      }
    }
  }

  .file-item{
    margin: 1em .625em 0 0;
    width: 11.875em;
    padding: .75em .5em .25em 1em;
    background-position: right 1.38em bottom -1em;
    &__title,
    &__format{
      font-size: .8125em;
    }
  }
}


@media screen and (max-width: 576px){
  .photo-field{
    position: relative;
    .avatar{
      position: absolute;
      left: 0;
      top: 0;
      width: 4.375em;
      height: 4.375em;
    }
    &__action{
      padding-left: 5.5em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      @include lastM(.625em 0 0);
      height: 1.875em;
      padding: 0 .75em;
      span{
        font-size: .75em;
      }
      svg{
        width: 1.125em;
      }
    }
    &__delete{
      order: 2;
      padding: 0;
      width: 1.875em;
    }
    &__desc{
      margin-top: .875em;
      .text15{
        font-size: .6875em;
        line-height: 145%;
      }
    }
  }

  .project-files{
    padding-bottom: .3125em;
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
      .file-item{
        width: 100%;
        margin: 0 !important;
      }
    }
    .project-files__items{
      margin-top: 0;
    }
    &__hint{
      display: none;
    }
  }

  .file-input{
    height: 4.125em;
    margin: .5em 0 0;
    width: 100%;
    grid-column: 1/-1;
    min-height: unset;
    &__title{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 427px){
  .project-files{
    &__items{
      grid-template-columns: 100%;
      gap: .5em;
    }
    .project-files__items{
      gap: .625em;
    }
  }

  .file-item{
    background-position: right 1.37em bottom 0;
  }
}
