.freelancer-top{
  margin-top: -1em;
  &__inner{
    padding: 3.5em 4.25em;
    background-image: url(../img/freelancer-bg.webp);
    background-color: $secondary;
    background-size: cover;
    background-position: center;
    border-radius: 1em;
    margin-top: 1.44em;
    position: relative;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1.44em;
    .icon-btn{
      margin-left: 1.625em;
    }
  }
  .page-top__text{
    max-width: 57em;
  }
}

.freelancer-info{
  max-width: 52.5625em;
  color: #fff;
  display: flex;
  align-items: center;
  .avatar{
    position: relative;
    width: 7.5em;
    height: 7.5em;
    margin-right: 1.25em;
  }
  .service-card__user-online{
    width: 1em;
    height: 1em;
    border-width: .125em;
    top: .8125em;
    right: .3125em;
  }
  &__text{
    @include grid100gap(.5em);
  }
  &__name{
    font-size: 1.25em;
    font-weight: 500;
    line-height: 125%;
  }
  &__wrap{
    margin-top: 1em;
    display: flex;
  }
  &__item,
  &__review{
    img{
      width: .875em;
      margin-right: .625em;
    }
    .text15{
      transform: translateY(10%);
    }
  }
  &__item{
    margin-left: 3.94em;
    img{
      width: 1.25em;
    }
  }
}

.freelancer-info2{
  position: absolute;
  top: 2em;
  right: 2em;
  padding: 1em 1.875em 1.7em;
  border-radius: 0.5em;
  background-color: #fff;
  box-shadow: $shadow;
  border: $border;
  width: 23.125em;
  &__btn{
    margin-top: 1em;
    width: 100%;
    box-shadow: 0 .3125em 1.25em rgba(94, 103, 255, 0.30);
  }
}

.prop{
  display: flex;
  justify-content: space-between;
  padding: .625em 0;
  border-bottom: $border;
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 186%;
  }
  &__title{
    color: $gray;
    font-weight: 300;
  }
  &__desc{
    font-weight: 500;
  }
}

.icon-btn{
  display: flex;
  align-items: center;
  font-weight: 500;
  &:hover{
    color: $blue;
  }
  &__round{
    transition: inherit;
    @include center;
    width: 2.25em;
    height: 2.25em;
    border: $border;
    box-shadow: $shadow;
    margin-right: .625em;
    flex-shrink: 0;
    border-radius: 50%;
    svg,
    img{
      width: .75em;
    }
  }
  &__text{
    font-size: .9375em;
    line-height: 130%;
  }
  &._added{
    path{
      fill: red;
      stroke: red;
    }
  }
}

.freelancer{
  overflow: hidden;
  padding: 2.69em 0 7.42em;
  position: relative;
  z-index: 5;
  &._overflow-unset{
    overflow: unset;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 23.125em) 23.125em;
    justify-content: space-between;
    gap: 5em 0;
    padding-right: 2.5em;
  }
  &__inner{
    max-width: 57.6875em;
  }
  &__col2{
    margin-top: -10.2em;
  }
  .article-item{
    width: 100%;
    max-width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}

.about-freelancer{
  margin-top: 3.125em;
  .text{
    margin-top: 1.25em;
  }
}

.text{
  h3{
    font-size: 1.25em;
    font-weight: 500;
    @include lastM(0 0 1em);
  }
  p{
    font-size: .9375em;
    line-height: 187%;
    font-weight: 300;
    @include lastM(0 0 1.25em);
  }
  hr{
    display: block;
    opacity: 0;
    height: 4.25em;
  }
}

.freelancer-services{
  padding-top: 2.19em;
  margin-top: 2em;
  border-top: $border;
  .product-slider{
    margin-top: 1em;
    .swiper{
      &-slide{
        @media screen and (min-width: 993px){
          width: 22.3125em;
          padding-right: 1.75em;
        }
      }
    }
  }
}

.review-rating{
  margin-top: 1.75em;
  display: flex;
  align-items: center;
  &__balls{
    @include grid100gap(.625em);
    max-width: 25.375em;
    flex-grow: 1;
  }
}

.review-stat{
  margin-right: 3.56em;
  padding: 3.25em 1em;
  @include center;
  flex-direction: column;
  border-radius: 0.625em;
  background-color: $primaryWhite;
  width: 18.75em;
  flex-shrink: 0;
  text-align: center;
  &__num{
    font-size: 3.75em;
    font-weight: 700;
    color: $primary;
  }
  &__info{
    margin-top: .1875em;
  }
}

.ball{
  display: grid;
  grid-template-columns: 4.875em calc(100% - 6.875em) 2em;
  align-items: center;
  &__type{
    font-size: .9375em;
    line-height: 187%;
  }
  &__progress{
    border-radius: 0.25em;
    height: .25em;
    background-color: $primaryWhite;
    position: relative;
    span{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      border-radius: inherit;
      background-color: $primary;
    }
  }
  &__count{
    font-size: .875em;
    line-height: 200%;
    color: $gray;
    text-align: right;
  }
}

.stat{
  display: flex;
  justify-content: space-between;
}

.stat-card{
  display: flex;
  align-items: center;
  max-width: 32%;
  &__icon{
    margin-right: 1.19em;
    flex-shrink: 0;
    width: 3em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .freelancer-top{
    padding-top: .5em;
    &._pb85{
      .page-top__inner{
        padding-bottom: 5.3125em;
      }
    }
    .container{
      padding: 0;
    }
    &__inner{
      margin: 0;
      padding: 1.94em 1.25em;
      border-radius: 0;
      background-image: unset;
    }
    &__nav{
      display: none;
    }
    .page-top__text{
      padding-left: 1.25em;
      padding-right: 1.25em;
      text-align: left;
    }
  }

  .freelancer-info{
    align-items: flex-start;
    width: 100%;
    .avatar{
      width: 4.4375em;
      height: 4.4375em;
      margin-right: 1.5625em;
    }
    &__content{
      flex-grow: 1;
    }
    &__name{
      font-size: 1em;
    }
    &__prof{
      font-size: .75em;
    }
    &__wrap{
      margin-top: 0;
      flex-wrap: wrap;
    }
    &__review{
      width: 100%;
    }
    &__items{
      margin: 1.375em 0 0 -6em;
      width: calc(100% + 6em);
      justify-content: space-between;
    }
    &__item,
    &__review{
      span{
        font-size: .75em;
        line-height: 200%;
      }
    }
    &__review{
      img{
        margin-right: .5em;
        width: .75em;
      }
    }
    &__item{
      margin: 0;
      img{
        width: .875em;
      }
    }
  }

  .freelancer-info2{
    position: static;
    width: 100%;
    margin: 1.375em 0 -6.8125em;
    padding: .25em 1.19em .9375em;
    &__btn{
      margin-top: .82em;
    }
  }

  .prop{
    padding: .625em 0;
    &__title,
    &__desc{
      font-size: .75em;
    }
  }

  .freelancer{
    padding: 7em 0 3.1875em;
    &._pt0{
      padding-top: 0;
    }
    &__grid{
      grid-template-columns: 100%;
      padding: 0;
    }
    &__col2{
      order: -1;
      margin-top: -3.8em;
      margin-right: 0;
    }
  }

  .about-freelancer{
    margin-top: 2.9em;
    .text{
      margin-top: .81em;
    }
  }

  .text{
    h3{
      @include lastM(0 0 .85em);
    }
    p{
      font-size: .875em;
      line-height: 171%;
    }
    hr{
      height: 1.85em;
    }
  }

  .freelancer-services{
    margin-top: 3em;
    padding-top: 0;
    border-top: none;
  }

  .stat{
    display: grid;
    grid-template-columns: 1fr .8fr;
    gap: 1.72em 1em;
  }

  .stat-card{
    max-width: unset;
    &__icon{
      width: 2.4375em;
      margin-right: 1.56em;
    }
    &__title{
      font-size: .875em;
      line-height: 130%;
    }
    &__desc{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .review-rating{
    display: block;
  }

  .review-rating{
    display: block;
  }

  .review-stat{
    width: 100%;
    margin: 0 0 1em;
  }

  .ball{
    &__type{
      font-size: .875em;
    }
  }
}



@media screen and (max-width: 427px){
  .freelancer-top{
    &__inner{
      padding: 1.31em .9375em;
    }
    .page-top__text{
      padding-left: .9375em;
      padding-right: .9375em;
    }
  }

  .freelancer-info{
    &__items{
      margin-top: .875em;
      @include grid100gap(.25em);
    }
  }

  .freelancer-info2{
    margin: 1.25em 0 -5.4375em;
    padding: .25em .875em .9375em;
    &__btn{
      margin-top: .875em;
    }
  }

  .freelancer{
    padding-top: 6.5em;
    &__grid{
      gap: 2.5em;
    }
  }

  .stat{
    padding: 0 2.5em;
    grid-template-columns: 100%;
    gap: 1.12em;
  }
}
