.header{

}

.header-top{
  padding: 1.3125em 0;
  display: flex;
  align-items: center;
  &__logo{
    display: block;
    width: 9.3125em;
    margin-right: 3em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.master-menu{
  position: relative;
  margin-right: 2em;
  &__toggle{
    display: flex;
    align-items: center;
    height: 2.75em;
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    svg{
      width: 1em;
      margin-right: .625em;
    }
    span{
      font-size: .9375em;
      line-height: 187%;
      transform: translateY(5%);
    }
  }
  &:hover{
    .menu{
      opacity: 1;
      pointer-events: unset;
      transform: translateY(0);
    }
  }
}

.menu{
  transition: all .3s ease;
  position: absolute;
  opacity: 0;
  pointer-events: none;
  top: 99%;
  left: 0;
  transform: translateY(.625em);
  width: 20.625em;
  padding: 1.25em 0;
  border-radius: 0.25rem 0rem 0rem 0.25rem;
  border: $border;
  z-index: 10;
  background-color: #fff;
  box-shadow: $shadow;
  &>li{
    padding-right: 2px;
    &:hover{
      .menu__dropdown{
        background: #F3F4FF;
        color: $black;
        border-color: $blue;
        &:before{
          opacity: 1;
        }
      }
      .drop-menu{
        transform: translateX(1.8%);
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__dropdown{
    display: flex;
    align-items: center;
    padding: 1.06em 1.75em;
    position: relative;
    transition: all .3s;
    border-left: 2px solid transparent;
    width: calc(100% + 1px);
    margin-left: -1px;
  }
  &__icon{
    width: 1.375em;
    margin-right: .9375em;
  }
  &__title{
    font-size: .9375em;
    line-height: 187%;
    font-weight: 500;
  }
}

.drop-menu{
  transition: all .3s ease;
  transform: translateX(-1%);
  position: absolute;
  top: -1px;
  left: calc(100% - .875em);
  width: 40.625em;
  min-height: calc(100% + 2px);
  border-radius: 0 0.25em 0.25em 0;
  border: $border;
  background-color: #fff;
  z-index: -1;
  opacity: 0;
  pointer-events: none;
  padding: 1.875em 2em .5em;
  &__grid{
    column-count: 3;
    column-gap: 1.875em;
  }
}

.submenu{
  display: inline-block;
  width: 100%;
  @include lastM(0 0 2em);
  &__list{
    margin-top: 1em;
    color: $gray;
    @include grid100gap(.75em);
    a{
      font-size: .875em;
      line-height: 150%;
    }
  }
}

.master-nav{
  display: flex;
  &>li{
    position: relative;
    @include lastM(0 2em 0 0);
    &:hover{
      .master-nav__item{
        color: $primary;
        path{
          fill: $primary;
        }
      }
      .master-nav__submenu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__item{
    height: 2.75em;
    display: flex;
    align-items: center;
    svg{
      margin-left: .875em;
      width: .375em;
    }
  }
  &__title{
    font-size: .9375em;
    line-height: 140%;
    transform: translateY(5%);
  }
  &__submenu{
    position: absolute;
    top: 100%;
    transform: translateY(-.25em);
    padding: .625em 0;
    border-radius: 0.25em;
    border: $border;
    background-color: #fff;
    box-shadow: $shadow;
    width: 16.4375em;
    z-index: 10;
    opacity: 0;
    pointer-events: none;
    li > a{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: .5em .81em .5em 1.75em;
      position: relative;
      transition: all .3s;
      border-left: 2px solid transparent;
      width: calc(100% + 2px);
      margin-left: -1px;
      font-size: .9375em;
      min-height: 3em;
      &:after{
        content: '';
        flex-shrink: 0;
        margin-left: .25em;
        width: .625em;
        height: .625em;
        background-image: url(../img/arrow-right2.svg);
        @include bgImgSize(100%);
        transition: inherit;
        opacity: 0;
      }
      &:hover{
        background-color: $primaryWhite;
        border-color: $primary;
        &:after{
          opacity: 1;
        }
      }
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  margin-left: auto;
  padding-left: .5em;
  &__btn{
    height: 2.75em;
    padding: 0 1.75em;
  }
  &__login{
    margin: 0 1.34em 0 2.56em;
    font-weight: 500;
    span{
      font-size: .9375em;
    }
  }
  &__search{
    width: 1em;
    @include center;
    margin-right: 1.81em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.header-bottom{
  border-top: $border;
  border-bottom: $border;
  .master-nav{
    justify-content: space-between;
    &__item{
      height: 3.25em;
      position: relative;
      &:before{
        background-color: $primary;
        bottom: -1px;
        content: "";
        height: .25em;
        left: 50%;
        position: absolute;
        width: 0;
        transition: all .3s ease;
      }
      &:hover,
      &._active{
        color: $black;
        &:before{
          left: 0;
          right: 0;
          width: 100%;
        }
      }
    }
    &>li{
      &:hover a{
        color: $black;
      }
      &:last-child{
        .master-nav__submenu{
          left: unset;
          right: 0;
        }
      }
    }
    &__submenu{
      transform: translateY(0);
    }
  }
}

.header-action2,
.mob-menu{
  display: none;
}

.mob-menu{
  &__links{
    @include grid100gap(.125em);
  }
  &__link{
    padding: .625em 1.125em;
    display: flex;
    align-items: center;
    min-height: 2.375em;
    font-weight: 500;
    svg{
      width: 1.125em;
      margin-right: 1.06em;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $primary;
      circle,
      path{
        fill: #fff;
      }
    }
  }
  &__title{
    font-size: .875em;
    line-height: 130%;
    transform: translateY(5%);
  }
  &__num{
    margin-left: auto;
    height: 1em;
    min-width: 1em;
    padding: 0 .25em;
    background-color: #FF6262;
    color: #fff;
    @include center;
    border-radius: 50%;
    span{
      display: block;
      font-size: .5em;
      transform: translateY(15%);
    }
  }
  &__account{
    margin-top: .5em;
    @include grid100gap(.375em);
    &-top{
      padding: 0 1.125em;
    }
  }
}

.header-action2{
  &__btn{
    width: 1.5em;
    @include center;
    margin-right: .6875em;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
    &-pin{
      position: absolute;
      top: .25em;
      right: .1875em;
      width: 0.4375em;
      height: 0.4375em;
      border-radius: 50%;
      background-color: #FF4C4C;
    }
    &._mess{
      .header-action2__btn-pin{
        right: 0;
      }
    }
  }
  &__user{
    margin-left: .5625em;
  }
}


@media screen and (max-width: 992px){
  .master-menu,
  .master-nav,
  .header-action,
  .header-bottom{
    display: none;
  }

  ._open-menu{
    .burger{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .admin-menu,
    .mob-menu{
      transform: translateX(0);
    }
  }

  .header{
    &._open{

    }
  }

  .header-top{
    padding: 1em 0;
    &__logo{
      width: 8.625em;
      position: relative;
      z-index: 21;
    }
  }

  .header-action2{
    display: flex;
    align-items: center;
    margin-left: auto;
    position: relative;
    z-index: 21;
  }

  .burger{
    position: relative;
    @include center;
    width: 1.25em;
    height: 1.25em;
    margin-left: 1.25em;
    flex-shrink: 0;
    img{
      @include centerAbsolute;
      width: 100%;
    }
    ._view2{
      width: 1em;
      opacity: 0;
    }
  }

  .admin-menu,
  .mob-menu{
    transition: all .4s;
    transform: translateX(-100%);
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    z-index: 20;
    display: block;
    padding: 4.625em 0 1.25em;
    overflow-y: auto;
  }
}


@media screen and (max-width: 427px){
  .mob-menu{
    &__account-top,
    &__link{
      padding-left: .9375em;
      padding-right: .9375em;
    }
  }

  .header-top{
    &__logo{
      width: 6.8125em;
    }
  }

  .header-action2{
    &__btn,
    &__user{
      display: none;
    }
  }
}
