.page-search{
  width: 50em;
  position: relative;
  margin-left: 1em;
  flex-shrink: 0;
  &__icon{
    position: absolute;
    top: 50%;
    left: 1.875em;
    transform: translateY(-50%);
    z-index: 2;
    width: 1em;
  }
  &__field{
    input{
      height: 5em;
      padding: 0 8.5em 0 3.5em;
      background-color: #fff;
    }
  }
  &__btn{
    position: absolute;
    right: .6875em;
    top: 50%;
    transform: translateY(-50%);
    box-shadow: $shadow3;
  }
  .category-select{
    position: absolute;
    right: 8em;
    top: 50%;
    transform: translateY(-50%);
  }
}

.page-search2{
  .page-search__field{
    input{
      padding-right: 25.5em;
    }
  }
}

.category-select{
  z-index: 3;
  .m-select{
    &__toggle{
      min-width: 17em;
      height: 2.5em;
      padding: 0 1.44em 0 1.875em;
      border-left: $border;
      justify-content: space-between;
    }
    &__current{
      font-weight: 400;
    }
  }
}

.services-items{
  margin-top: 1.875em;
  @include grid100gap(1.875em);
}

.service-card2{
  padding: 1.875em 1.875em 1.44em;
  border-radius: 0.625em;
  border: $border;
  transition: all .3s;
  display: grid;
  grid-template-columns: 3.75em calc(100% - 24em) 18.75em;
  gap: 1.25em;
  &:hover{
    box-shadow: $shadow;
  }
  .avatar{
    width: 3.75em;
    height: 3.75em;
    border-radius: .625em;
    position: relative;
  }
  .service-card__user-online{
    top: .25em;
    right: .25em;
    width: .5625em;
    height: .5625em;
    border-width: 1px;
  }
  &__props{
    display: flex;
    margin-top: .875em;
  }
  &__prop{
    display: flex;
    align-items: center;
    color: $gray;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 143%;
      transform: translateY(5%);
    }
    &:not(:last-child){
      padding-right: 2.125em;
      border-right: $border;
      margin-right: .625em;
    }
  }
  &__text{
    margin-top: 1.19em;
    max-width: 33.75em;
    font-weight: 300;
  }
  &__action{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-left: 1.81em;
    border-left: $border;
    margin-bottom: .5em;
  }
  &__btn{
    width: 100%;
    margin-top: 1em;
    height: 3.125em;
    span{
      font-size: .875em;
    }
  }
}

.service-info{
  margin-top: 2.94em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 3em / 4));
  gap: 1em;
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 187%;
  }
  &__title{
    font-weight: 500;
  }
  &__desc{
    margin-top: .5em;
    font-weight: 300;
  }
}

.services2{
  padding: 0 0 10em;
  background-color: transparent;
  .m-section__top{
    margin-bottom: 2.25em;
  }
}

.gallery{
  margin-top: 2.5em;
  overflow: hidden;
  .swiper{
    &-button{
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      width: 3.75em;
      height: 3.75em;
      border-radius: 50%;
      @include center;
      background-color: #fff;
      box-shadow: 0 .625em 2.1875em rgba(5, 16, 54, 0.10);
      svg{
        width: 1.0625em;
      }
      &-prev{
        left: 1.94em;
      }
      &-next{
        right: 1.94em;
      }
    }
  }
  &__big{
    position: relative;
    .swiper{
      overflow: hidden;
    }
  }
  &__small{
    margin-top: .625em;
    .swiper{
      &-slide{
        width: 10em;
        padding-right: .625em;
      }
    }
  }
  &__img{
    @include imgCover(74.4%);
    border-radius: .25em;
    display: block;
  }
}

.services-items2{
  margin-top: 1.5em;
}

.services3{
  background-color: transparent;
  padding: 2.44em 0 2em;
  .m-section__top{
    margin-bottom: 1.5em;
  }

  @media screen and (min-width: 993px){
    .product-slider{
      &__slide{
        width: 20%;
      }
      &__nav{
        position: absolute;
        right: 0;
        bottom: 100%;
        margin-bottom: 1.5em;
        .swiper-pagination{
          display: flex;
        }
        .swiper-button{
          background-color: transparent;
          width: 1.25em;
          height: auto;
          box-shadow: none;
          position: static;
          transform: unset;
          svg{
            width: 100%;
          }
          ._view2{
            display: none;
          }
          ._view1{
            display: block;
          }
        }
      }
    }
    .service-card{
      &__type{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      &__content{
        padding: .625em .625em 1.25em 1.25em;
      }
      &__user{
        &-name{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .page-search{
    margin: 1.64em 0 0;
    width: 100%;
    position: relative;
    &__icon{
      left: 1.125em;
      width: .875em;
    }
    &__field{
      input{
        font-size: .75em;
        height: 3.91em;
        padding: 0 8em 0 3.5em;
      }
    }
    &__btn{
      height: 2.5em;
      padding: 0 1.5em;
      right: .375em;
      span{
        font-size: .8125em;
      }
    }
    .category-select{
      right: 6em;
    }
  }

  .page-search2{
    .page-search__field{
      input{
        padding-right: 28.5em;
      }
    }
  }

  .category-select{
    .m-select{
      &__toggle{
        min-width: 15em;
        padding-left: 1em;
        padding-right: 1em;
      }
    }
  }

  .service-card2{
    padding: 0;
    border: none;
    grid-template-columns: 100%;
    gap: .5em;
    position: relative;
    .avatar{
      width: 3.125em;
      height: 3.125em;
    }
    &__price{
      position: absolute;
      top: 0;
      right: 0;
      height: 3.125em;
      display: flex;
      align-items: center;
    }
    &__props{
      margin-top: .75em;
    }
    &__prop{
      img{
        margin-right: .375em;
      }
      span{
        font-size: .625em;
      }
      &:not(:last-child){
        padding-right: .81em;
        margin-right: .81em;
      }
    }
    &__text{
      margin-top: .75em;
      max-width: 100%;
      .text15{
        font-size: .8125em;
        line-height: 169%;
      }
    }
    &__action{
      margin: 0;
      padding: 0;
      border: none;
    }
    &__btn{
      margin: 0;
    }
  }

  .service-info{
    grid-template-columns: repeat(3,calc(100% / 3 - 1.75em / 3));
    gap: .875em;
    &__desc{
      font-size: .875em;
      line-height: 171%;
      margin-top: .625em;
    }
  }

  .services2{
    padding: 0 0 2.94em;
    background-color: transparent;
    .m-section__top{
      margin-bottom: 1.75em;
    }
  }

  .gallery{
    margin-top: 2em;
  }

  .services3{
    padding: 1.125em 0;
    .m-section__top{
      margin-bottom: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .service-info{
    grid-template-columns: 100%;
  }

  .gallery{
    &__small{
      margin-top: .5em;
      .swiper{
        width: calc(100% + .5em);
        &-slide{
          width: calc(100% / 3);
          padding-right: .5em;
        }
      }
    }
    .swiper{
      &-button{
        width: 1.875em;
        height: 1.875em;
        &-prev{
          left: 1.31em;
        }
        &-next{
          right: 1.31em;
        }
        svg{
          width: .5625em;
        }
      }
    }
  }

  .page-search2{
    .page-search__field{
      input{
        padding-right: 12em;
      }
    }
  }

  .category-select{
    .m-select{
      &__toggle{
        border: none;
        min-width: unset;
        background-image: url(../img/filter.svg);
        @include bgImgSize(1.125em);
        width: 3.25em;
        svg{
          display: none;
        }
      }
      &__current{
        display: none;
      }
      &__dropdown{
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
}


@media screen and (max-width: 427px){
  .page-search{
    margin-top: .94em;
  }

  .gallery{
    &__small{
      margin-top: .375em;
      .swiper{
        width: calc(100% + .375em);
        &-slide{
          padding-right: .375em;
        }
      }
    }
  }
}
