@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.19em;
    height: 3.875em;
    border-radius: 0.25em;
    font-weight: 500;
    position: relative;
    overflow: hidden;
    &:before{
        content: "";
        height: 100%;
        left: -100%;
        position: absolute;
        top: 0;
        width: 0;
        //z-index: -1;
        transform: skew(50deg);
        transform-origin: top left;
        transition: all .4s ease;
    }
    &:focus {
        box-shadow: none;
    }
    span{
        transform: translateY(10%);
        font-size: .9375em;
        line-height: 140%;
    }
    span,img,svg{
        position: relative;
        z-index: 2;
    }
    &:hover{
        &:before{
            height: 100%;
            width: 200%;
        }
    }
}

.m-btn-blue{
    background-color: $primary;
    color: #fff;
    &:before{
        background-color: #7078FF;
    }
    &:hover{
        color: #fff;
    }
}

.m-btn-red{
    background-color: #D64C4C;
    color: #fff;
    &:before{
        background-color:darken(#D64C4C,10%);
    }
    &:hover{
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $black;
    &:hover{
        color: $black;
    }
    &:before{
        background-color: #E3E6FF;
    }
}

.m-btn-gray{
    background-color: #505050;
    color: #fff;
    &:hover{
        color: #fff;
    }
    &:before{
        background-color: #6C6C6C;
    }
}

.m-btn-blue-outline{
    border: 2px solid $primary;
    color: $primary;
    &:hover{
        color: $primary;
    }
    &:before{
        background-color: #E3E6FF;
    }
}

.m-btn-blue2,
.m-btn-blue3{
    color: $primary;
    &:hover{
        color: #fff;
    }
    &:before{
        background-color: $primary;
    }
}

.m-btn-blue2{
    background-color: rgba(163, 140, 230, 0.18);
}

.m-btn-blue3{
    background-color: $primaryWhite;
}

.hover-fill-primary{
    &:hover{
        path{
            fill: $primary;
        }
    }
}

.m-btn-shadow{
    box-shadow: $shadow3;
    font-weight: 700;
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.125em;
        padding: 0 1em;
        span{
            font-size: .875em;
        }
    }

    .m-btn-shadow{
        font-weight: 500;
    }
}
