.catalog{
  padding: 2.19em 0 5.44em;
  &__grid{
    display: grid;
    grid-template-columns: 18.75em calc(100% - 18.75em - 3.56em);
    gap: 0 3.56em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.sort{
  display: flex;
  align-items: center;
  .m-select{
    margin-left: .25em;
    &__dropdown{
      left: unset;
      right: 0;
    }
  }
}

.m-select{
  position: relative;
  &._open{
    .m-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._not-select{
    .m-select__toggle{
      color: $gray;
      font-weight: 300;
    }
  }
  &__toggle{
    display: flex;
    align-items: center;
    transition: all .3s;
    cursor: pointer;
    &:hover{
      color: $primary;
      &>svg path{
        fill: $primary;
      }
    }
    &>svg{
      width: .375em;
      margin-left: .75em;
    }
  }
  &__current{
    font-weight: 500;
    font-size: .9375em;
  }
  &__dropdown{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    border-radius: 0.375em;
    border: 2px solid #D9D9D9;
    background-color: #fff;
    min-width: 100%;
    padding: .625em 0;
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .25em;
    z-index: 5;
  }
  &__option{
    padding: .25em 1.375em;
    display: flex;
    align-items: center;
    transition: all .3s;
    cursor: pointer;
    min-height: 2.1875em;
    &:hover,
    &._active{
      color: $black;
      background-color: $primaryWhite;
    }
    span{
      font-size: .9375em;
      line-height: 110%;
    }
  }
  .m-check{
    label{
      &:before{
        transform: translateY(8%);
      }
    }
  }
  &__tag{
    display: flex;
    align-items: center;
    flex-shrink: 0;
    height: 2.125em;
    border-radius: 3.75em;
    background-color: $primaryWhite;
    padding: 0 .94em;
    margin-right: .25em;
    span{
      font-size: .8125em;
    }
    &:hover{
      color: $black;
    }
    &-remove{
      width: .375em;
      @include center;
      margin-left: .56em;
      svg{
        width: 100%;
      }
      &:hover{
        path{
          fill: $primary;
        }
      }
    }
  }
}

._multi-select{
  .m-select__dropdown{
    padding: 1em 1.375em .75em;
  }
  .m-select__current{
    display: flex;
  }
}

.filter-btn{
  display: none;
}

.freelancers-items{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 3.5em / 3));
  gap: 1.38em 1.75em;
  margin-top: 1.125em;
}

.freelance-card{
  padding: 1.875em 1.875em 1.5em;
  border-radius: 1.25em;
  border: $border;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  &:hover{
    border-color: $primary;
    color: $black;
  }
  .avatar{
    width: 5.625em;
    height: 5.625em;
    margin: 0 auto 1.25em;
    position: relative;
  }
  .service-card__user-online{
    width: 1em;
    height: 1em;
    border-width: 2px;
    top: .8125em;
    right: -.0625em;
  }
  &__info{
    text-align: center;
    @include grid100gap(.25em);
  }
  &__name{
    font-size: 1.0625em;
    line-height: 160%;
    font-weight: 500;
    display: inline-block;
    margin: 0 auto;
  }
  &__prof{
    font-size: .875em;
    line-height: 150%;
    color: $gray;
  }
  .service-card__reviews{
    margin: 0;
    justify-content: center;
  }
  &__stat{
    margin-top: 1.625em;
    padding-top: .875em;
    border-top: $border;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: 1em .5em;
    align-items: flex-start;
    &-group{
      @include grid100gap(.25em);
    }
    &-title{
      font-size: .9375em;
      line-height: 185%;
      font-weight: 500;
    }
    &-desc{
      font-size: .875em;
      line-height: 130%;
      font-weight: 300;
    }
  }
  &__nav{
    padding-top: 1em;
    margin-top: auto;
    .m-btn{
      width: 100%;
    }
  }
}

.filter{
  &__top{
    display: none;
  }
}

.filter-group{
  &:not(:last-child){
    margin-bottom: 1.875em;
    padding-bottom: 1.25em;
    border-bottom: $border;
  }
  &._active{
    .filter-group__top svg{
      transform: rotate(-180deg);
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.125em;
    svg{
      width: .5em;
    }
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 500;
  }
  &__checks{
    @include grid100gap(.875em);
    &:not(._show-all){
      .m-check{
        &:nth-child(4)~.m-check{
          display: none;
        }
      }
    }
    &._show-all{
      .more-checks{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }
  .blog-field{
    margin-bottom: 1.25em;
    padding: 1px;
    input{
      font-size: .875em;
      padding: 0 3em 0 1.21em;
      height: 3.21em;
    }
    &__btn{
      width: 1em;
      right: .9375em;
    }
  }
}

.filter-check{
  &__wrap{
    display: flex !important;
    align-items: center;
    justify-content: space-between;
    &:before{
      transform: translateY(5%)
    }
  }
  &__title{
    font-size: .9375em;
    line-height: 130%;
  }
}

.more-checks{
  display: inline-block;
  margin-right: auto;
  font-size: 0.9375em;
  line-height: 187%;
  font-weight: 500;
  color: $primary;
  &:hover{
    color: $secondary;
  }
  ._view2{
    display: none;
  }
}

.filter-slider{
  padding: 1px;
  &__item{
    min-height: .9375em;
    padding: 0 .375em 0 .5em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #E9E9E9;
    border: none;
    box-shadow: none;
    height: .25em;
    border-radius: .375em;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: 2px solid $primary;
      background-color: #fff;
      width: .875em;
      height: .875em;
      top: -.375em !important;
      right: -.375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: .3125em 0 .6875em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.25em;
    &-title{
      position: absolute;
      top: 50%;
      left: 1.25em;
      transform: translateY(-50%);
    }
  }
  .fg{
    position: relative;
    input{
      height: 3.21em;
      color: $black;
      font-weight: 400;
      font-size: .875em;
      line-height: 140%;
      padding-left: 1em;
      padding-right: 2em;
      border-radius: .4em;
    }
    label{
      font-size: .9375em;
      font-weight: 400;
      margin: 0;
    }
  }
  &__currency{
    height: 2.8125em;
    position: absolute;
    right: 0;
    bottom: 0;
    padding-right: .875em;
    display: flex;
    align-items: center;
    color: $gray;
    span{
      font-size: 0.875em;
      line-height: 120%;
    }
  }
  &__output{
    margin-top: .875em;
    font-size: 1em;
    line-height: 175%;
    text-align: center;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding: 1.5em 0 3em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__count{
      display: none;
    }
  }

  .sort{
    &__title{
      text-transform: capitalize;
      font-size: .75em;
      font-weight: 500;
      span{
        display: none;
      }
    }
    .m-select__toggle{
      svg{
        margin-left: .69em;
      }
    }
    .m-select__current{
      font-size: .75em;
    }
  }

  .filter-btn{
    padding: 0 1.25em;
    width: 11.0625em;
    height: 2.8125em;
    border-radius: 0.25em;
    display: flex;
    justify-content: space-between;
    font-weight: 500;
    color: $black;
    svg{
      width: 1.0625em;
    }
    &:hover{
      path{
        fill: #fff;
      }
    }
  }

  .freelancers-items{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    margin-top: 1.5em;
  }

  .freelance-card{
    padding: 1.5em 1.44em;
    border-radius: 0.25rem;
    box-shadow: $shadow;
    &__top{
      display: flex;
    }
    .avatar{
      width: 4.375em;
      height: 4.375em;
      margin: 0 1.25em 0 0;
    }
    &__info{
      text-align: left;
      gap: 0;
    }
    &__name{
      margin: 0;
      font-size: .9375em;
    }
    &__prof{
      font-size: .75em;
    }
    .service-card__reviews{
      .text14{
        font-size: .75em;
      }
    }
    &__stat{
      margin-top: .875em;
      &-group{
        gap: 0;
      }
      &-title{
        font-size: .875em;
      }
      &-desc{
        font-size: .8125em;
      }
    }
  }

  .open-filter{
    .filter{
      transform: translateX(0);
    }
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    z-index: 30;
    padding: 1.25em;
    overflow-y: auto;
    transition: all .5s;
    transform: translateX(-120%);
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 1.25em;
      margin-bottom: 1.25em;
      border-bottom: $border;
    }
    &__close{
      width: 1em;
      @include center;
      img{
        width: 100%;
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .freelancers-items{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 427px){
  .catalog{
    padding: 1.25em 0 1.69em;
  }

  .filter-btn{
    width: 9.0625em;
    flex-direction: row-reverse;
  }

  .freelancers-items{
    margin-top: .69em;
    gap: .69em;
  }
}
