.conditions{
  padding: 4.5em 0 7.5em;
  &__inner{
    display: grid;
    grid-template-columns: 19.25em calc(100% - 22.25em);
    gap: 3em;
  }
  &__tabs{
    position: sticky;
    top: 1em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-left: $border;
  }
  &__tab{
    margin-left: -1px;
    min-height: 2.8125em;
    display: flex;
    align-items: center;
    padding-left: 1.125em;
    border-left: 2px solid transparent;
    transition: all .3s;
    font-weight: 500;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
    &._active{
      border-color: $primary;
      color: $primary;
    }
    span{
      font-size: .9375em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 992px){
  .conditions{
    padding: 2.81em 0 3.31em;
    &__inner{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 427px){
  .conditions{
    padding: 2em 0 3em;
  }
}
