.page-top{
  .blog-field{
    width: 29.75em;
    margin-left: 1em;
    flex-shrink: 0;
    input{
      background-color: #fff;
      border-color: #fff;
    }
  }
}

.knowledge{
  padding: 6.25em 0 3em;
  &__grid{
    row-gap: 1.5em;
  }
  .advantage-card{
    border-radius: 0.5em;
    padding: 2.5em 2.5em 2.31em;
    border: $border;
    box-shadow: $shadow;
    &__icon{
      height: 3.25em;
    }
    &__info{
      gap: .5em;
    }
    &__desc{
      color: $gray;
    }
  }
}

.faq-page{
  padding: 6.25em 0;
  &__items{
    @include grid100gap(6.25em);
    max-width: 57.75em;
    margin: 0 auto;
    width: 100%;
  }
  &__item{
    @include grid100gap(2em);
  }
}


@media screen and (max-width: 992px){
  .page-top{
    .blog-field{
      width: 100%;
      margin: .875em 0 0;
      input{
        border-radius: .33em;
      }
    }
  }

  .knowledge{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
    .advantage-card{
      text-align: left;
      padding: 1.125em 1.31em 1.38em;
      position: relative;
      &__info{
        margin: 0;
        gap: 1.25em;
      }
      &__title{
        font-size: 1.125em;
        line-height: 139%;
        min-height: 2.77em;
        display: flex;
        align-items: center;
        padding-left: 4.72em;
      }
      &__icon{
        position: absolute;
        top: 1.125em;
        left: 1.31em;
      }
    }
  }

  .faq-page{
    padding: 2.5em 0 3em;
    &__items{
      gap: 2.5em;
    }
    &__item{
      gap: 1em;
    }
    &__title{
      text-align: center;
    }
  }
}


@media screen and (max-width: 576px){
  .knowledge{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 427px){
  .faq-page{
    &__items{
      gap: 3.125em;
    }
  }
}
