.dashboard{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
  gap: 2em 1.875em;
  margin-top: 2em;
}

.dashboard-card{
  padding: 0 1.875em 1.875em;
  border-radius: .4375em;
  background: #FFF;
  display: flex;
  flex-direction: column;
  &._big2{
    grid-column: 1/-1;
  }
  &._big{
    grid-column: span 2;
    .dashboard-card__bottom{
      width: calc(100% + 2em);
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 4em;
    border-bottom: $border;
    position: relative;
    z-index: 3;
  }
  .m-select,
  &__link,
  &__title{
    transform: translateY(7%);
  }
  &__more{
    &-btn{
      width: .1875em;
      position: relative;
      &:before{
        content: '';
        @include centerAbsolute;
        width: 1em;
        height: 1em;
      }
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        circle{
          fill: $primary;
        }
      }
    }
  }
  &__bottom{
    flex-grow: 1;
    min-height: 20em;
    display: flex;
    align-items: center;
  }
  .chart{
    height: 16em;
    width: calc(100% + 5em);
    margin-right: -1.5em;
    @include center;
  }
  .line-chart{
    margin: 0 0 0 -1.5em;
    height: auto;
  }
  &__link{
    text-decoration: underline;
    color: #6440FB;
    span{
      font-size: 0.875em;
      line-height: 120%;
    }
  }
  &__services{
    padding-top: .625em;
  }
  &__buys,
  &__projects{
    align-self: flex-start;
    width: 100%;
  }
}

.apexcharts-legend{
  justify-content: center !important;
  font-family: $font1;
  overflow: unset !important;
  &-series{
    display: flex;
    align-items: flex-start;
    margin: 12px 0 !important;
  }
  &-marker{
    width: 0.5625em !important;
    height: 0.5625em !important;
    top: 1px !important;
    left: -2px !important;
  }
  &-title{
    font-size: 15px;
    line-height: 120%;
    font-weight: 500;
  }
  &-num{
    font-size: 14;
    line-height: 120%;
    opacity: 0.7;
    color: $gray;
  }
}

.line-chart{
  .apexcharts-xaxistooltip{
    display: none;
  }
  .apexcharts-tooltip{
    opacity: 1 !important;
    pointer-events: unset !important;
    box-shadow: 0px 0px 8px rgba(16, 30, 115, 0.12);
    border: none !important;
    padding: .25em;
    //transform: translate(58%,-50%);
    @include center;
    &-title{
      display: none;
    }
    &-marker{
      display: none;
    }
    &-text-y{
      &-label{
        display: none;
      }
      &-value{
        margin: 0 !important;
        text-align: center;
      }
    }
    &-y-group{
      transform: translateY(3px);
    }
  }
}

.service-card3{
  padding: 1.25em 0;
  display: grid;
  grid-template-columns: 7.5em calc(100% - 8.5em);
  gap: 1em;
  border-bottom: $border;
  &:last-child{
    border-bottom: none;
    padding-bottom: 0;
  }
  .service-card{
    &__img{
      border-radius: .25em;
      padding-bottom: 75%;
    }
    &__content{
      padding: 0;
      display: flex;
    }
    &__bottom{
      border: none;
      padding-top: .25em;
    }
    &__title{
      line-height: 160%;
    }
    &__reviews{
      margin: 0;
    }
  }
}

.buy-card{
  padding: 1.25em 0;
  border-bottom: $border;
  display: flex;
  &:last-child{
    border-bottom: none;
    padding-bottom: 0;
  }
  .avatar{
    width: 3.75em;
    height: 3.75em;
    margin-right: .94em;
  }
  &__content{
    padding-top: .25em;
  }
  &__title{
    @include truncate(2);
    a{
      font-size: .9375em;
      line-height: 160%;
    }
    span{
      color: $primary;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1em;
  }
  &__price{
    font-size: .9375em;
    line-height: 187%;
    font-weight: 500;
  }
  &__date{
    font-size: .875em;
    line-height: 200%;
    font-weight: 300;
    color: $gray;
  }
}

.activity{
  padding-top: 1.875em;
  height: 100%;
  width: calc(100% + 1em);
}

.activity-item{
  display: grid;
  grid-template-columns: 2.3em calc(100% - 5.3em);
  gap: 3em;
  color: $dark;
  position: relative;
  margin-bottom: .375em;
  &._red{
    &:before{
      border-color: #F64E60;
    }
  }
  &._blue{
    &:before{
      border-color: #3699FF;
    }
  }
  &._orange{
    &:before{
      border-color: #EF6327;
    }
  }
  &._purple{
    &:before{
      border-color: #8950FC;
    }
  }
  &:last-child{
    margin-bottom: 0;
    &:after{
      display: none;
    }
  }
  &:before{
    content: '';
    position: absolute;
    left: 3em;
    top: .125em;
    width: 0.8125em;
    height: 0.8125em;
    border-radius: 50%;
    border: 3px solid #663259;
  }
  &:after{
    content: '';
    position: absolute;
    left: 3.3125em;
    bottom: 0;
    height: calc(100% - 1.44em);
    background: #E5EAEE;
    width: 0.1875em;
    border-radius: 0.5em;
    background: #E5EAEE;
  }
  &__time{
    font-size: .875em;
    line-height: 157%;
    text-align: right;
  }
  &__text{
    font-size: .9375em;
    line-height: 147%;
  }
}

.project-card{
  padding: 1.25em 0;
  border-bottom: $border;
  display: grid;
  grid-template-columns: calc(100% - 41em) 18em 8em 12em;
  gap: 1em;
  align-items: center;
  &:last-child{
    padding-bottom: 0;
    border-bottom: none;
  }
  &__title{
    font-size: 1.0625em;
    line-height: 165%;
    font-weight: 500;
  }
  .reply-row__props{
    margin-top: .44em;
  }
  &__price,
  &__type{
    font-size: .9375em;
    line-height: 187%;
    font-weight: 300;
  }
  &__nums{
    display: flex;
    align-items: center;
    &-title{
      font-size: .9375em;
      line-height: 187%;
    }
  }
  &__count{
    @include center;
    width: 1.375em;
    height: 1.375em;
    background-color: #FF6262;
    border-radius: 50%;
    color: #fff;
    font-weight: 700;
    flex-shrink: 0;
    margin-right: .75em;
    span{
      font-size: 0.6875em;
      transform: translateY(7%);
    }
  }
}


@media screen and (max-width: 992px){
  .dashboard{
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: -.75em;
  }

  .dashboard-card{
    padding: 0 1.25em 1.25em;
    &._big{
      grid-column: 1/-1;
      .dashboard-card__bottom{
        width: calc(100% + 1em);
      }
    }
    &__top{
      min-height: 3.69em;
    }
    &__bottom{
      min-height: 12em;
    }
    .chart{
      height: 12em;
    }
    .line-chart{
      height: auto;
    }
    &__services{
      padding-top: 0;
    }
    &__link{
      span{
        font-size: .75em;
      }
    }
  }

  .service-card3{
    padding: .9375em 0;
  }

  .activity{
    padding-top: 1.25em;
  }

  .project-card{
    grid-template-columns: 100%;
    gap: .81em;
    padding: 1.125em 0;
  }
}


@media screen and (max-width: 576px){
  .dashboard-card{
    &__top{
      &._type2{
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        min-height: 4.75em;
      }
    }
    &__link{
      margin-top: .25em;
    }
  }

  .service-card3{
    grid-template-columns: 100%;
    gap: .5em;
    .service-card{
      &__img{
        padding-bottom: 49%;
      }
      &__title{
        font-size: .875em;
        line-height: 171%;
      }
    }
  }

  .buy-card{
    .avatar{
      margin-right: 1.5em;
    }
    &__content{
      display: flex;
      flex-direction: column;
    }
    &__bottom{
      min-height: 3.75em;
      flex-direction: column;
      align-items: flex-start;
      justify-content: unset;
      order: -1;
      margin: 0;
    }
    &__title{
      width: calc(100% + 5.25em);
      margin-left: -5.25em;
      margin-top: 1.25em;
    }
  }

  .activity-item{
    grid-template-columns: 2.375em calc(100% - 3.0625em);
    gap: .8125em;
    &:before{
      top: 1.625em;
      left: 1.56em;
    }
    &:after{
      height: calc(100% - 3.8125em);
      top: 3.125em;
      left: 1.875em;
    }
    &__text{
      font-size: .875em;
      line-height: 157%;
      padding-top: 1.62em;
    }
  }
}
