.chat{
  display: grid;
  grid-template-columns: 25.9375em calc(100% - 25.9375em - 1.875em);
  gap: 1.875em;
  &._active{
    .chat__content{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__sidebar,
  &__content{
    height: calc(100 * var(--vh) - 22em);
    border-radius: 0.4375em;
    background: #FFF;
  }
  &__content{
    opacity: 0;
    pointer-events: none;
  }
  &__back{
    display: none;
  }
}

.chat-search{
  height: 3.8125em;
  border-bottom: $border;
  input{
    border: none;
    font-size: .875em;
    height: 100%;
    padding-left: 4.21em;
    &:focus{
      background-color: transparent;
      outline: none !important;
    }
  }
  &__icon{
    position: absolute;
    left: 1.81em;
    top: 50%;
    transform: translateY(-50%);
    width: 1em;
  }
}

.chat-items{
  overflow-y: auto;
  height: calc(100% - 3.8125em);
  @include scroll;
}

.chat-item{
  padding: .94em 2.5em;
  display: flex;
  transition: all .3s;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: .25em;
    height: calc(100% - 1.4em);
    border-radius: .625em 0 0 .625em;
    background-color: $primary;
    opacity: 0;
    transition: inherit;
  }
  &:hover{
    background: #F1F3FF;
    cursor: pointer;
  }
  &._active{
    background: #F1F3FF;
    &:before{
      opacity: 1;
    }
  }
  .avatar{
    width: 3.125em;
    height: 3.125em;
    align-self: center;
    margin-right: .625em;
  }
  &__name{
    font-size: .9375em;
    line-height: 175%;
    font-weight: 500;
  }
  &__prof{
    font-size: .8125em;
    line-height: 175%;
    font-weight: 300;
    color: $gray;
  }
  &__nums{
    margin-left: auto;
    padding-left: 1em;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__time{
    font-size: .625em;
    line-height: 280%;
    color: $gray;
    font-weight: 300;
  }
  &__count{
    min-width: 1em;
    height: 1em;
    margin-top: .125em;
    border-radius: 50%;
    padding: 0 .125em;
    background-color: $primary;
    color: #fff;
    font-weight: 700;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    span{
      font-size: .625em;
      line-height: 130%;
      transform: translateY(7%);
    }
  }
}

.chat-content{
  display: none;
  height: 100%;
  &._type2{
    .chat-content__top{
      height: 6.625em;
      padding-bottom: 3.1875em;
    }
    .chat-status{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      justify-content: center;
      span{
        font-size: .75em;
      }
      img{
        width: .875em;
      }
    }
  }
  &._active{
    display: block;
  }
  &__top{
    position: relative;
    display: flex;
    align-items: center;
    padding: 1.31em 2.56em 1.19em;
    border-bottom: $border;
    height: 5.625em;
  }
  &__order{
    font-weight: 300;
    margin-left: 6.12em;
    img{
      width: 1.5em;
      margin-right: .875em;
    }
    span{
      font-size: .9375em;
      line-height: 160%;
      transform: translateY(10%);
    }
  }
  &__right{
    margin-left: auto;
  }
  &__delete{
    font-weight: 300;
    color: $gray;
    &:hover{
      color: $red;
      path{
        fill: $red;
      }
    }
    svg{
      width: 1.375em;
      margin-right: .375em;
    }
    span{
      font-size: .875em;
      line-height: 130%;
      transform: translateY(7%);
    }
  }
}

.message-user,
.chat-user{
  display: flex;
  align-items: center;
  .avatar{
    width: 3.125em;
    height: 3.125em;
    margin-right: 1.25em;
  }
  &__name{
    font-size: .9375em;
    line-height: 155%;
    font-weight: 500;
  }
}

.chat-user{
  &__status{
    display: flex;
    align-items: center;
    color: $primary;
    &:before{
      content: '';
      flex-shrink: 0;
      margin-right: .3125em;
      width: 0.3125em;
      height: 0.3125em;
      border-radius: 50%;
      background-color: $primary;
    }
    span{
      font-size: .875em;
      line-height: 150%;
    }
  }
}

.chat-nav{
  height: 7.5em;
  border-top: $border;
  justify-content: space-between;
  padding: 1.81em 1.875em;
  &__btn{
    width: 13.375em;
    height: 3.75em;
    margin-left: 2.06em;
    flex-shrink: 0;
    span{
      font-size: .875em;
    }
  }
  &__file{
    margin-left: 1em;
    flex-shrink: 0;
    &:hover{
      .chat-nav__file-btn{
        background-color: $primary;
        path{
          fill: #fff;
        }
      }
    }
    &-btn{
      width: 2.5em;
      height: 2.5em;
      @include center;
      border-radius: .3125em;
      background-color: $primaryWhite;
      svg{
        width: 1.5625em;
      }
    }
  }
  &__field{
    flex-grow: 1;
    textarea{
      width: 100%;
      height: 100%;
      border: none;
      resize: none;
      font-size: 0.875em;
      line-height: 130%;
      color: $black;
      &::placeholder{
        color: $gray;
      }
    }
  }
}

.messages{
  height: calc(100% - 13.125em);
  overflow-y: auto;
  padding: 2.5em;
  @include scroll;
  &__split{
    @include center;
    margin-bottom: 1.56em;
    position: relative;
    font-weight: 300;
    color: $gray;
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 100%;
      height: 1px;
      background: #C8CBE8;
    }
    &-text{
      padding: 0 1.71em;
      background-color: #fff;
      position: relative;
      z-index: 2;
      transform: translateY(5%);
    }
  }
}

.message-item{
  @include lastM(0 0 1.25em);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &._own{
    align-items: flex-end;
    .message-user{
      flex-direction: row-reverse;
      .avatar{
        margin: 0 0 0 1.25em;
      }
      &__time{
        margin: 0 1.25em 0 0;
      }
    }
    @media screen and (min-width: 993px){

    }
    .message-item__text{
      border: 1px solid rgba(#C8CBE8,.5);
      background-color: transparent;
    }
  }
  &._judge{
    .message-item__text{
      background: rgba(243, 157, 71, 0.13);
    }
  }
  &__content{
    margin: .94em 0 0 0;
    max-width: 28.5em;
  }
  &__text{
    padding: 1.19em 1.81em;
    border-radius: 0.5em;
    background-color: $primaryWhite;
    font-size: .9375em;
    line-height: 160%;
    font-weight: 300;
    *{
      line-height: inherit;
    }
  }
  &__files{
    display: flex;
    .file-item{
      margin: 0;
      width: 17.4375em;
      max-width: 100%;
      padding: .5em 1.375em;
    }
  }
}

.message-user{
  &__time{
    margin-left: 1.25em;
    font-weight: 300;
    color: $gray;
    span{
      font-size: .875em;
      line-height: 130%;
    }
  }
}

.chat-status{
  margin-right: 3.38em;
  height: 2.4375em;
  display: flex;
  align-items: center;
  padding: 0 1em;
  border-radius: 0.25em;
  background-color: $primaryWhite;
  color: $primary;
  span{
    font-size: .9375em;
    transform: translateY(7%);
  }
  img{
    width: 1.25em;
    margin-right: .56em;
  }
}


@media screen and (max-width: 992px){
  .chat{
    grid-template-columns: 100%;
    width: calc(100% + 2em);
    margin-left: -1em;
    position: relative;
    &__back{
      align-items: center;
      position: absolute;
      right: 1em;
      bottom: 100%;
      margin-bottom: 1em;
      color: $gray;
      span{
        font-size: .875em;
      }
    }
    &__content{
      display: none;
    }
    &__sidebar,
    &__content{
      height: calc(100 * var(--vh) - 17em);
    }
    &._active{
      .chat__sidebar{
        display: none;
      }
      .chat__content{
        display: block;
      }
      .chat__back{
        display: flex;
      }
    }
  }

  .chat-content{
    &__top{
      padding: .75em 1em;
      height: 4.375em;
    }
    &__order{
      display: none;
    }
    &__delete{
      svg{
        margin-right: .25em;
      }
      span{
        font-size: .75em;
      }
    }
  }

  .chat-user{
    .avatar{
      width: 2.375em;
      height: 2.375em;
      margin-right: .875em;
    }
    &__name{
      font-size: .875em;
    }
    &__status{
      span{
        transform: translateY(7%);
        font-size: .625em;
      }
    }
  }

  .messages{
    height: calc(100% - 11.75em - 4.375em);
    padding: 1em;
    &__split{
      margin-bottom: .44em;
      &:after{
        display: none;
      }
      &-text{
        background-color: transparent;
        padding: 0;
        font-size: .625em;
        line-height: 280%;
      }
    }
  }

  .chat-nav{
    height: 11.75em;
    flex-direction: column;
    padding: .625em 1em;
    align-items: stretch;
    &__action{
      flex-direction: column;
      align-items: flex-start;
      margin: 0;
    }
    &__btn{
      height: 3.125em;
      order: -1;
      width: 100%;
      margin: 0;
    }
    &__file{
      margin: .875em auto 0;
    }
  }

  .message-user{
    width: 100%;
    .avatar{
      width: 2.375em;
      height: 2.375em;
      margin-right: .875em;
    }
    &__name{
      font-size: .75em;
    }
    &__time{
      margin-left: auto;
      span{
        font-size: .75em;
      }
    }
  }

  .message-item{
    &._own{
      .message-user{
        &__time{
          margin-right: auto;
        }
        .avatar{
          margin-left: .875em;
        }
      }
    }
    &__text{
      font-size: .75em;
      line-height: 167%;
      padding: .83em 1.33em;
      border-radius: .67em;
    }
  }
}


@media screen and (max-width: 576px){
  .chat-item{
    padding: .9375em;
    &__name{
      font-size: .875em;
    }
    &__prof{
      font-size: .75em;
    }
  }
}
