.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 180%;
    font-weight: 500;
    margin-bottom: .31em;
  }
  textarea,
  input {
    border-radius: 0.25em;
    border: $border;
    padding: 0 1em;
    height: 3.4375em;
    width: 100%;
    font-size: 1em;
    line-height: 120%;
    padding-top: .3125em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $primary;
      //border-color: #E9E9E9;
      background-color: $primaryWhite;
      outline: 1px solid $primary !important;
    }
  }
  textarea{
    height: 16.0625em;
    padding-top: 1em;
    padding-bottom: 1em;
    resize: none;
  }
  .m-select{
    &__toggle{
      padding: 0 .94em;
      height: 3.4375em;
      border-radius: 0.25em;
      border: $border;
      justify-content: space-between;
      svg{
        width: .5625em;
      }
    }
  }
}

.chat-nav__file,
.req-answer,
.file-input,
.pay-way__item,
.m-switch,
.m-check,
.m-radio{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  span{
    font-size: .9375em;
    line-height: 160%;
  }
  ._radio,
  ._check,
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.6225em;
    min-height: 1.56em;
    padding-top: .0625em;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: .25em;
      border: 1px solid $primary;
      width: 1em;
      height: 1em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+._check ,
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .5em auto;
      }
    }
  }
  ._check,
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check3.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~._radio,
  input:checked~label{
    &:before{
      border-width: 3px;
      border-color: $primary;
    }
  }
  ._radio,
  label{
    &:before{
      border-radius: 50%;
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    textarea{
      height: 14em;
    }
    input{
      height: 2.9375em;
      padding: 0 1em;
    }
    &._small{
      input,
      textarea{
        border-radius: .33em;
        font-size: .75em;
        height: 3.91em;
        padding-left: 1.58em;
        padding-right: 1.58em;
      }
    }
  }
}
