.saves{
  padding: 1.06em 1.875em;
  background-color: #fff;
  border-radius: .4375em;
  .tabs{
    margin-bottom: 1.375em;
  }
  .pagination{
    padding-top: 4.5em;
  }
}

.tabs{
  display: flex;
  border-bottom: 1px solid $primaryWhite;
}

.m-tab{
  color: $gray;
  transition: all .3s;
  @include lastM(0 2em 0 0);
  padding-bottom: .5em;
  margin-bottom: -1px;
  border-bottom: 2px solid transparent;
  &._active{
    color: $black;
    font-weight: 500;
    border-color: $primary;
  }
  &:hover{
    cursor: pointer;
    color: $black;
  }
  span{
    font-size: 1.0625em;
    line-height: 165%;
  }
}

.reviews-page{
  padding: 1em 1.875em 1.75em;
  background-color: #fff;
  border-radius: .4375em;
  .tabs{
    margin-bottom: 1.56em;
  }
  .review-card2{
    &:not(:last-child){
      padding-bottom: 1.875em;
      margin-bottom: 1.875em;
      border-bottom: $border;
    }
    &__text{
      .text15{
        font-size: 1em;
        line-height: 150%;
      }
    }
  }
}

.payments{
  th,td{
    &:first-child{
      width: 25em;
    }
    &:nth-child(2){
      width: 22em;
    }
    &:last-child{
      width: 12em;
    }
  }
  td{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    border-bottom: $border;
  }
}

.pay-row{
  &__title{
    display: none;
  }
  &__badges{
    display: flex;
  }
  &__desc{
    font-size: .9375em;
    line-height: 187%;
  }
}

.m-badge{
  padding: 0 .72em;
  border-radius: 0.375em;
  background: rgba(94, 202, 130, 0.17);
  display: flex;
  align-items: center;
  height: 2.125em;
  font-weight: 500;
  color: $green;
  span{
    font-size: .8125em;
    line-height: 120%;
    transform: translateY(5%);
  }
  &._yellow{
    color: #FFA800;
    background: #FFF4DE;
  }
  &._red{
    color: #F1416C;
    background: #FFF5F8;
  }
}

.pay-form{
  margin-top: 1.875em;
  padding: 1.875em;
  border-radius: 0.25em;
  background: #FFF;
  &__top{
    padding-bottom: 1.25em;
    border-bottom: $border;
    .text17{
      transform: translateY(5%);
    }
  }
  &__items{
    max-width: 58.125em;
    margin-top: 1.875em;
    @include grid100gap(1.875em);
  }
  &__address{
    max-width: 28.125em;
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
  &__btn{
    width: 13.375em;
    height: 3.75em;
  }
}

.pay-way{
  &__wrap{
    margin-top: .625em;
    display: flex;
  }
  &__items{
    padding: .31em .44em;
    border-radius: 3.75em;
    background-color: $primaryWhite;
    display: flex;
  }
  &__item{
    @include lastM(0 .4375em 0 0);
    input:checked~label{
      background-color: #fff;
    }
    label{
      height: 2.1875em;
      border-radius: 3.75em;
      padding: 0 1.25em;
      display: flex;
      align-items: center;
      font-weight: 500;
      transition: all .3s;
    }
    span{
      font-size: .875em;
      line-height: 130%;
      transform: translateY(5%);
    }
  }
}


@media screen and (max-width: 992px){
  .saves{
    padding: 0;
    background-color: transparent;
    .tabs{
      padding: 0 1em;
      margin-bottom: 1.25em;
    }
    .pagination{
      padding-top: 1.94em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .tabs{
    border-bottom: none;
  }

  .m-tab{
    min-width: 7em;
    display: flex;
    justify-content: center;
    padding-bottom: .24em;
  }

  .reviews-page{
    padding: .75em 0 1.5em;
    .tabs{
      padding: 0 1em;
      margin-bottom: 1.25em;
    }
    .review-card2{
      gap: .6875em;
      padding-left: 1em;
      padding-right: 1em;
      &:not(:last-child){
        padding-bottom: 1.375em;
        margin-bottom: 1.375em;
      }
      &__text{
        .text15{
          font-size: .875em;
          line-height: 157%;
        }
      }
      .avatar{
        width: 3.125em;
        height: 3.125em;
        margin-right: 1.06em;
      }
    }
  }

  .payments{
    padding: 0;
    background-color: transparent;
    tbody{
      display: grid;
      gap: .875em;
      grid-template-columns: repeat(2,calc(50% - .4375em));
    }
  }

  .pay-row{
    padding: 1.25em;
    background-color: #fff;
    border-radius: 0.4375em;
    @include grid100gap(.625em);
    td{
      width: 100% !important;
      border: none;
      padding: 0;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
      &:last-child{
        .pay-row__title{
          display: flex;
          align-items: center;
        }
      }
    }
    &__title{
      display: block;
      font-size: .75em;
      line-height: 233%;
    }
    &__desc{
      font-weight: 500;
      word-break: break-word;
      line-height: 145%;
    }
  }

  .pay-form{
    padding: .69em 1.125em 1.5em;
    &__top{
      padding: 0 1.125em .69em;
      width: calc(100% + 2.25em);
      margin-left: -1.125em;
      .text17{
        font-size: 1em;
      }
    }
    &__items{
      margin-top: 1.5em;
      gap: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .saves{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .payments{
    tbody{
      grid-template-columns: 100%;
    }
  }

  .pay-form{
    &__btn{
      height: 3em;
      width: 100%;
    }
  }

  .pay-way{
    &__items{
      padding: .625em;
      width: 100%;
      flex-direction: column;
      border-radius: 0.875em;
      align-items: center;
    }
    &__item{
      margin: 0 !important;
    }
  }
}
