._flex{
  display: flex;
  align-items: center;
}

.cut-section{
  width: 100%;
  max-width: 1700px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 0.625em;
  @media screen and (max-width: 1700px){
    border-radius: 0;
  }
}

.main{
  padding: 5.625em 0 4em;
  background-color: $secondary;
  color: #fff;
  &__inner{
    position: relative;
  }
  &__content{
    max-width: 37.5em;
  }
  &__text{
    max-width: 36.25em;
    @include grid100gap(2.06em)
  }
  &__nav{
    margin-top: 2.88em;
    display: inline-grid;
    grid-template-columns: auto auto;
    gap: 1.06em;
  }
}

.main-partners{
  margin-top: 4em;
  &__items{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: calc(100% + 4.38em);
  }
  &__item{
    margin: .69em 4.38em 0 0;
    max-width: 7.75em;
    height: 2em;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}

.main-offer{
  position: absolute;
  top: -1.25em;
  right: 3.125em;
  width: 27.9375em;
  &__img{
    @include imgCover(119%);
    border-radius: 0.625em;
    ._view2{
      display: none;
    }
  }
}

.main-advantage{
  position: absolute;
  top: 4.25em;
  right: 100%;
  margin-right: -6.25em;
  padding: 1.25em;
  border-radius: 1em;
  background-color: #fff;
  box-shadow: .625em .625em 2.5em rgba(34, 34, 34, 0.05);
  display: flex;
  align-items: center;
  color: $black;
  width: 18em;
  &._type2{
    top: unset;
    bottom: 4.81em;
    left: unset;
    right: -3.19em;
    margin: 0;
  }
  &__icon{
    margin-right: 1.25em;
    flex-shrink: 0;
    width: 3.125em;
    height: 3.125em;
    @include center;
    border-radius: 50%;
    background-color: rgba($primary,.1);
    img{
      max-width: 1.875em;
    }
  }
  .text13{
    font-weight: 300;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1.5em 0;
    &__content{
      max-width: 100%;
    }
    &__text{
      max-width: 100%;
      gap: .625em;
      text-align: center;
    }
    &__nav{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
      margin-top: 1.5em;
    }
  }

  .main-partners{
    margin-top: 2em;
    &__items{
      @include fullScreenTablet;
      flex-wrap: nowrap;
      margin-top: 1em;
      overflow-x: auto;
      padding-bottom: 1em;
    }
    &__item{
      flex-shrink: 0;
      height: 1.9em;
      margin: 0 3.125em 0 0;
      max-width: 7em;
    }
  }

  .main-offer{
    position: relative;
    top: unset;
    right: unset;
    width: 100%;
    margin-top: 1.56em;
    &__img{
      padding-bottom: 58.8%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .main-advantage{
    top: .625em;
    left: .625em;
    right: unset;
    margin: 0;
    padding: .75em .5em .75em 1em;
    width: 6.875em;
    flex-direction: column;
    align-items: flex-start;
    border-radius: .625em;
    &._type2{
      right: .625em;
      bottom: .625em;
    }
    &__icon{
      width: 2.25em;
      height: 2.25em;
      margin: 0 0 .3125em;
      img{
        max-width: 1.35em;
      }
    }
    &__content{
      @include grid100gap(.3125em);
    }
    .text15{
      font-size: .8125em;
      line-height: 140%;
    }
    .text13{
      font-size: .6875em;
      line-height: 140%;
    }
  }
}

@media screen and (max-width: 427px){
  .main{
    &__text{
      gap: 1.06em;
    }
    &__nav{
      grid-template-columns: 100%;
      margin-top: 2.44em;
    }
  }

  .main-partners{
    &__title{
      text-align: center;
    }
    &__items{
      @include fullScreenMob;
    }
  }

  .main-offer{
    display: none;
  }
}
