.projects-table{
  th,td{
    &:nth-child(1){
      width: auto;
    }
    &:nth-child(2){
      width: 19em;
    }
    &:nth-child(3){
      width: 10em;
    }
  }
  .tabs{
    margin-bottom: 1.94em;
  }
}

.tabs-select{
  &__show{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .projects-table{
    th,td {
      padding: 0;
      width: 100% !important;
      height: unset !important;
      border: none;
      &:nth-child(2) {
        position: static;
      }
    }
  }

  ._mob-select{
    &._open{
      .tabs-select__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .tabs-select{
    margin-bottom: 1.06em;
    position: relative;
    &__show{
      height: 3.125em;
      border-radius: 0.3125em;
      border: 1px solid #D4D6FA;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 1.31em;
      &:after{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/tab-arrow.svg);
        width: 0.375em;
        height: 0.3125em;
        @include bgImgSize(100%);
      }
    }
    &__current{
      font-size: 1.0625em;
      line-height: 165%;
      font-weight: 500;
      color: $primary;
      transform: translateY(5%);
    }
    &__hidden{
      display: block;
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: .5em;
      width: 100%;
      z-index: 10;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    .tabs{
      margin: 0;
      display: grid;
      grid-template-columns: 100%;
      background-color: #fff;
      border-radius: 0.25em;
      border: $border;
      box-shadow: $shadow;
    }
    .m-tab{
      min-height: 2.1875em;
      height: unset;
      padding: .25em 1.875em;
      border: none;
      min-width: unset;
      margin: 0 !important;
      font-weight: 300;
      justify-content: unset;
      align-items: center;
      span{
        font-size: .9375em;
        line-height: 120%;
      }
      &._active{
        background-color: $primaryWhite;
      }
    }
  }
}
