.page-top{
  margin-top: 1em;
  &__inner{
    padding: 3em 3.63em;
    border-radius: 0.625em;
    background-color: $secondary;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__text{
    color: #fff;
  }
  &__desc{
    margin-top: .46em;
  }
  &__items{
    display: flex;
    margin-top: 1em;
  }
  &__item{
    display: flex;
    align-items: center;
    @include lastM(0 2.5em 0 0);
    img{
      width: 1.25em;
      margin-right: .625em;
    }
    span{
      font-size: .9375em;
      line-height: 187%;
      transform: translateY(5%);
    }
    b{
      font-weight: 500;
    }
    ._view2{
      display: none;
    }
    &._review{
      img{
        width: .875em;
      }
    }
  }
  &__user{
    display: flex;
    align-items: center;
    margin-right: 3em;
    .avatar{
      width: 2.5em;
      height: 2.5em;
      margin-right: .94em;
      position: relative;
    }
    .service-card__user-online{
      border-width: 1px;
      width: 0.5625em;
      height: 0.5625em;
      top: .06em;
      right: .06em;
    }
    &-name{
      font-size: .9375em;
      line-height: 187%;
      transform: translateY(5%);
    }
  }
}

.blog-page{
  padding: 3.69em 0 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 29.75em) 23.125em;
    gap: 1.875em 6.625em;
  }
  &__content{
    grid-column: 1/2;
    grid-row: 1/8;
  }
  &__items{
    @include grid100gap(3.75em);
  }
}

.blog-field{
  input{
    padding-left: 1.38em;
  }
  &__btn{
    position: absolute;
    top: 50%;
    right: 1.06em;
    transform: translateY(-50%);
    width: 1.25em;
    @include center;
    svg{
      width: 100%;
    }
  }
}

.blog-tags,
.blog-category,
.blog-recent{
  border-radius: 0.5em;
  border: $border;
  padding: 1.875em;
}

.blog-category{
  &__links{
    margin-top: 1.125em;
    @include grid100gap(1em);
  }
  &__link{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.m-count{
  @include center;
  padding: 0 .625em;
  height: 1.75em;
  border-radius: 3.125em;
  background-color: $primaryWhite;
  color: $gray;
  span{
    font-size: .75em;
    line-height: 130%;
    transform: translateY(5%);
  }
}

.blog-recent{
  &__items{
    margin-top: 1.75em;
    @include grid100gap(1em);
  }
}

.blog-card2{
  display: flex;
  align-items: center;
  max-width: 18.875em;
  &:hover{
    color: $black;
  }
  .avatar{
    margin-right: .94em;
    width: 4.375em;
    height: 4.375em;
    border-radius: .5em;
  }
  &__title{
    font-weight: 500;
    font-size: .9375em;
    line-height: 147%;
    transition: all .3s;
    &:hover{
      text-decoration: underline;
      color: $primary;
    }
  }
  &__date{
    margin-top: .25em;
  }
}

.blog-tags{
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.125em;
    .tag{
      margin: .625em .625em 0 0;
    }
  }
}

.tag{
  height: 2.125em;
  padding: 0 1.06em;
  border-radius: 3.75em;
  background-color: $primaryWhite;
  font-weight: 400;
  color: $black;
  span{
    font-size: .8125em;
  }
  &:before{
    background-color: #E3E6FF;
  }
  &:hover{
    color: $black;
  }
}

.blog-card3{
  display: grid;
  grid-template-columns: 20.5625em calc(100% - 20.5625em - 3.625em);
  gap: .25em 3.625em;
  align-items: center;
  &__img{
    @include imgCover(127.6%);
    border-radius: .625em;
  }
  &__text{
    @include grid100gap(.31em);
  }
  &__type{
    font-size: .875em;
    line-height: 200%;
    color: $gray;
  }
  &__title{
    line-height: 143%;
    &:hover{
      text-decoration: underline;
    }
  }
  &__user{
    display: flex;
    align-items: center;
    margin-top: 1.06em;
    &:hover{
      color: $black;
    }
    .avatar{
      width: 3.75em;
      height: 3.75em;
      margin-right: 1.25em;
    }
    &-title{
      font-size: .9375em;
      line-height: 187%;
      font-weight: 500;
    }
    &-desc{
      font-size: .875em;
      line-height: 185%;
      font-weight: 300;
      color: $gray;
    }
  }
}

.pagination{
  @include center;
  margin-top: 3.75em;
  &__item,
  &__btn{
    @include center;
    border-radius: 6.25em;
  }
  &__item{
    margin: 0 .375em;
    min-width: 2.67em;
    height: 2.67em;
    padding: 0 .125em;
    @include center;
    font-weight: 500;
    font-size: .9375em;
    &:hover{
      color: $black;
      background-color: $primaryWhite;
    }
    &._active{
      background-color: $primary;
      color: #fff;
    }
  }
  &__btn{
    width: 3.125em;
    height: 3.125em;
    border: $border;
    &._prev{
      margin-right: 1.44em;
    }
    &._next{
      margin-left: 1.44em;
    }
    svg{
      width: .9375em;
    }
    &:hover{
      border-color: $black;
    }
  }
  &__text{
    margin-top: 1.375em;
    text-align: center;
  }
}


@media screen and (max-width: 992px){
  .page-top{
    margin-top: 0;
    background-color: $secondary;
    &__inner{
      border-radius: 0;
      padding: 1.5em 0;
      display: block;
    }
    &__text{
      @include grid100gap(.625em);
      text-align: center;
    }
    &__desc{
      margin: 0;
    }
    &__nav{
      margin-top: 1.45em;
      .m-btn{
        width: 100%;
      }
    }
    &__items{
      margin-top: .375em;
    }
    &__item{
      @include lastM(0 1.94em 0 0);
      span{
        font-size: .75em;
        line-height: 233%;
      }
      img{
        width: .875em;
      }
    }
    &__user{
      margin-right: 1.94em;
      .avatar{
        margin-right: .6875em;
      }
      &-name{
        font-size: .75em;
        line-height: 130%;
      }
    }
  }

  .blog-page{
    padding: 1.5em 0 2.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: .9375em;
    }
    &__content{
      grid-row: unset;
      margin-top: .625em;
    }
  }

  .blog-field {
    input {
      padding-left: 1.38em;
      font-size: .75em;
      height: 4.125em;
      border-radius: .67em;
    }
    &__btn{
      right: 1.37em;
      width: 1em;
    }
  }

  .blog-recent{
    padding: 0;
    border: none;
    margin-top: 2.5em;
    &__items{
      margin-top: 1.25em;
    }
  }

  .blog-card2{
    max-width: 100%;
  }

  .blog-tags{
    margin-top: 1.5em;
    padding: .94em 1.125em 1.3125em;
    &__title{
      font-size: 1.125em;
    }
    &__items{
      margin: 0;
      .tag{
        margin: .81em .81em 0 0;
      }
    }
  }

  .blog-card3{
    grid-template-columns: 15em calc(100% - 16em);
    gap: 1em;
    &__text{
      gap: .25em;
    }
    &__type{
      font-size: .75em;
      line-height: 233%;
    }
    &__title{
      font-size: 1.5em;
      line-height: 125%;
    }
    &__desc{
      font-size: .875em;
      line-height: 171%;
      margin-top: .625em;
    }
    &__user{
      margin-top: 1.125em;
      .avatar{
        width: 2.5625em;
        height: 2.5625em;
        margin-right: 1.19em;
      }
      &-title{
        font-size: .875em;
        line-height: 150%;
      }
      &-desc{
        font-size: .75em;
        line-height: 150%;
      }
    }
  }

  .pagination{
    margin-top: 2.5em;
    &__btn{
      &._next,
      &._prev{
        margin: 0;
      }
    }
    &__item{
      margin: 0 .25em;
      &:nth-child(2){
        margin-left: 0;
      }
      &:nth-last-child(2){
        margin-right: 0;
      }
    }
    ._hide-tablet{
      display: none;
    }
    &__text{
      margin-top: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .blog-card3{
    grid-template-columns: 100%;
    &__img{
      padding-bottom: 67.5%;
    }
  }

  .page-top{
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 0 1em;
      &._gap10{
        row-gap: .625em;
      }
    }
    &__item{
      margin: 0 !important;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__user{
      margin-right: 0;
    }
  }
}


@media screen and (max-width: 427px){
  .page-top{
    &__nav{
      margin-top: 1.06em;
    }
  }

  .pagination{
    ._hide-mob{
      display: none;
    }
    &__item{
      margin: 0;
    }
    &__btn{
      width: 2.5em;
      height: 2.5em;
      &._prev{
        margin-right: auto;
      }
      &._next{
        margin-left: auto;
      }
    }
    &__text{
      .text14{
        font-size: .75em;
      }
    }
  }
}
