.m-table{
  padding: 1.75em 1.75em 2em;
  border-radius: .4375em;
  background: #FFF;
  table{
    width: 100%;
    border-spacing: 0;
    border-collapse: separate;
  }
  th{
    padding: 1.31em 1.875em;
    text-align: left;
    background-color: $primaryWhite;
    font-weight: 500;
    &:nth-child(1){
      border-radius: .375em 0 0 .375em;
    }
    &:last-child{
      border-radius:  0 .375em .375em 0;
    }
    span{
      font-size: 1.0625em;
      line-height: 165%;
      transform: translateY(10%);
      display: block;
    }
  }
  td{
    padding: 1.875em;
  }
}

.replies{
  th,td{
    &:nth-child(2){
      width: 14em;
    }
    &:last-child{
      width: 11.5em;
    }
  }
}

.reply-row{
  &__info{
    display: flex;
    align-items: center;
  }
  .avatar{
    width: 3.75em;
    height: 3.75em;
    margin-right: 1.25em;
  }
  &__props{
    display: flex;
    margin-top: .3125em;
  }
  &__prop{
    display: flex;
    align-items: center;
    color: $gray;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 143%;
      transform: translateY(5%);
    }
    &:not(:last-child){
      padding-right: 1.375em;
      border-right: $border;
      margin-right: .9375em;
    }
  }
  &__action{
    display: flex;
  }
  &__btn{
    position: relative;
    @include lastM(0 .3125em 0 0);
    .m-btn{
      padding: 0;
      width: 2.5em;
      height: 2.5em;
      border-radius: 0.3125em;
      svg{
        width: 1.25em;
      }
      &:before{
        left: -115%;
        transform: skew(50deg) scale(1.5);
      }
      &:hover{
        path{
          fill: #fff;
        }
      }
    }
    &:hover{
      .tooltip{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.tooltip{
