.attached-files{
  margin-top: 2em;
  padding-top: 3.75em;
  border-top: $border;
  .project-files__items{
    margin-top: .75em;
  }
  .file-item{
    margin-right: 1.25em;
  }
}


.freelancer-req{
  margin-top: 2em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .875em));
    gap: 1.25em 1.75em;
    margin-top: 1.75em;
  }
  &__items{
    margin-top: 3.75em;
    @include grid100gap(1.25em);
  }
}

.req-answer{
  &__inner{
    padding: 2.37em 1.875em 2em;
    border-radius: 0.25em;
    border: $border;
    display: flex;
    transition: all .3s;
  }
  ._check{
    flex-shrink: 0;
    z-index: 1;
    margin-right: .75em;
    border: 1px solid #1F4B3F;
    width: 1em;
    height: 1em;
    @include bgImgSize(0);
    transition: all .0s;
    border-radius: .25em;
  }
  input:checked~.req-answer__inner{
    border-color: $primary;
    outline: 1px solid $primary !important;
    background-color: $primaryWhite;
    ._check{
      background-color: $primary;
      border-color: $primary;
      background-image: url(../img/check3.svg);
      background-size: .5em auto;
    }
  }
  &__content{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 187%;
    transform: translateY(-15%);
  }
  &__price{
    font-weight: 700;
    span{
      font-size: 1.75em;
      line-height: 130%;
    }
  }
}

.freelancer-offers{
  margin-top: 3.75em;
  padding-top: 2.25em;
  border-top: $border;
  &__items{
    margin-top: 1.81em;
    @include grid100gap(1.875em);
  }
}

.offer-card{
  padding: 1.875em;
  border-radius: 0.25em;
  border: $border;
  display: grid;
  grid-template-columns: calc(100% - 11em) 10em;
  gap: .19em 1em;
  &__content{
    display: flex;
    align-items: center;
  }
  .avatar{
    width: 5.625em;
    height: 5.625em;
    margin-right: 1.25em;
    position: relative;
  }
  &__items{
    display: flex;
    margin-top: .5em;
  }
  &__item{
    display: flex;
    align-items: center;
    color: $gray;
    &:not(:last-child){
      padding-right: 1.75em;
      margin-right: .81em;
      border-right: $border;
    }
    &._rating{
      img{
        width: .625em;
        margin-right: .625em;
      }
      .offer-card__item-text{
        span{
          color: $black;
        }
      }
    }
    img{
      width: 1.25em;
      margin-right: .5em;
    }
    &-text{
      font-size: 0.875em;
      line-height: 143%;
      transform: translateY(5%);
    }
  }
  &__nums{
    text-align: right;
  }
  &__price{
    font-size: 1.25em;
    font-weight: 500;
    line-height: 140%;
  }
  &__period{
    font-size: .875em;
    line-height: 200%;
    color: $gray;
  }
  &__text{
    grid-column: 1/-1;
    padding-left: 6.875em;
    max-width: 49em;
  }
  .service-card__user-online{
    width: 1em;
    height: 1em;
    border-width: 2px;
    top: .81em;
    right: -.06em;
  }
}

.about-client,
.offer-req{
  background-color: #fff;
  border-radius: 0.5em;
  border: $border;
  box-shadow: $shadow;
}

.offer-req{
  padding: 1.37em 2.13em 1.94em;
  position: relative;
  z-index: 2;
  &__btn{
    margin-top: 1.25em;
    width: 100%;
  }
  &__text{
    @include grid100gap(.31em);
    padding-bottom: 1.625em;
    border-bottom: $border;
  }
  &__title{
    font-size: 1.0625em;
    line-height: 165%;
    font-weight: 500;
  }
  &__desc{
    font-size: .875em;
    line-height: 157%;
    font-weight: 300;
  }
  &__items{
    margin-top: 1.125em;
    @include grid100gap(.75em);
  }
  &__item{
    display: flex;
    &:before{
      content: '';
      position: relative;
      width: 1.25em;
      height: 1.25em;
      background-image: url(../img/check2.svg);
      background-size: 100% auto;
      background-repeat: no-repeat;
      background-position: center;
      top: .25em;
      margin-right: .875em;
    }
    &-text{
      padding-top: .5em;
      font-size: .875em;
      line-height: 150%;
    }
  }
  &__duration{
    margin-top: 1.25em;
    font-weight: 500;
    display: flex;
    align-items: center;
    img{
      width: 1.4375em;
      margin-right: .6875em;
    }
    span{
      transform: translateY(5%);
      font-size: .875em;
      line-height: 200%;
    }
  }
}

.about-client{
  padding: 1.5em 1.875em 1.9375em;
  margin-top: 1.875em;
  &__info{
    margin-top: 1.25em;
    display: flex;
    align-items: center;
  }
  .avatar{
    width: 5.625em;
    height: 5.625em;
    margin-right: 1.25em;
    position: relative;
  }
  .service-card__user-online{
    top: .81em;
    right: -.06em;
    border-width: 2px;
    width: 1em;
    height: 1em;
  }
  &__category{
    font-size: .875em;
    line-height: 150%;
    color: $gray;
  }
  &__rating{
    display: flex;
    align-items: center;
    img{
      width: .625em;
      margin-right: .625em;
    }
    &-text{
      font-size: .875em;
      line-height: 187%;
      font-weight: 300;
      color: $gray;
      transform: translateY(5%);
      span{
        font-weight: 500;
        color: $black;
      }
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
    gap: .5em;
    padding-top:  1.25em;
    margin-top: 1.875em;
    border-top: $border;
  }
  &__item{
    &-title,
    &-desc{
      font-size: .9375em;
      line-height: 150%;
    }
    &-title{
      font-weight: 500;
    }
    &-desc{
      font-weight: 300;
    }
  }
  &__btn{
    margin-top: 1.875em;
    width: 100%;
    height: 3.75em;
    font-weight: 700;
    span{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .freelancer-req{
    margin-top: 1.75em;
    padding-top: 2.625em;
    border-top: $border;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
      margin-top: 1em;
    }
    &__items{
      gap: .625em;
      margin-top: .875em;
    }
  }

  .req-answer{
    &__inner{
      padding: 1.3125em;
    }
    &__title,
    &__desc{
      font-size: .75em;
      line-height: 157%;
    }
    &__price{
      span{
        font-size: 1.25em;
      }
    }
  }

  .attached-files{
    margin-top: 2em;
    padding-top: 2em;
  }

  .freelancer-offers{
    margin-top: 1.875em;
    padding-top: 1.31em;
    &__items{
      margin-top: 1em;
      gap: 1.38em;
    }
  }

  .offer-card{
    padding: 0 0 1.75em;
    border-left: none;
    border-right: none;
    border-top: none;
    grid-template-columns: 100%;
    gap: 1em;
    .avatar{
      width: 3.45em;
      height: 3.45em;
      margin-right: 1.35em;
    }
    &__name{
      font-size: .875em;
      line-height: 143%;
    }
    &__item{
      &-text{
        font-size: .75em;
      }
    }
    &__nums{
      text-align: left;
      order: -1;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__price{
      font-size: 1em;
    }
    &__text{
      padding-left: 0;
      .text15{
        font-size: .875em;
        line-height: 171%;
      }
    }
    .service-card__user-online{
      width: .625em;
      height: .625em;
      border-width: 1.3px;
      top: .5em;
      right: -.04em;
    }
  }

  .offer-req{
    padding-left: 1.81em;
    padding-right: 1.8em;
    &__price{
      font-size: 1.5em;
      text-align: center;
    }
    &__btn{
      margin-top: 0;
    }
  }

  .offer-req2{
    .offer-req{
      &__price{
        text-align: left;
      }
      &__text{
        padding-bottom: 1.38em;
      }
      &__title{
        font-size: 1em;
        line-height: 175%;
      }
      &__desc{
        max-width: 19.3125em;
      }
      &__items{
        gap: .25em;
        margin: .25em 0 1.25em;
      }
      &__duration{
        img{
          width:  1.25em;
          margin-right: .875em;
        }
      }
    }
  }

  .about-client{
    padding: 1em 1.375em;
    &__info{
      margin-top: .875em;
    }
    .avatar{
      width: 4.5625em;
      height: 4.5625em;
      margin-right: 1.19em;
    }
    &__name{
      font-size: 1em;
      line-height: 175%;
    }
    &__category{
      font-size: .75em;
    }
    &__items{
      margin-top: .875em;
      padding-top: .875em;
    }
    &__item{
      &-title,
      &-desc{
        font-size: .75em;
        line-height: 180%;
      }
    }
    &__btn{
      margin-top: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .freelancer-req{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .req-answer{
    &__content{
      display: block;
    }
    &__price{
      margin-top: .38em;
    }
  }

  .offer-card{
    &__content{
      align-items: flex-start;
    }
    &__info{
      padding-top: .17em;
      flex-grow: 1;
    }
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1.17em .5em;
      width: calc(100% + 4.8em);
      margin-left: -4.8em;
      margin-top: 0;
    }
    &__item{
      &._rating{
        grid-column: 1/-1;
        padding-left: 4.8em;
      }
      &:not(:last-child){
        padding-right: 0;
        margin-right: 0;
        border: none;
      }
      &-text{
        line-height: 233%;
      }
    }
  }
}


@media screen and (max-width: 427px){
  .offer-req{
    padding: .5em 1em .875em;
  }

  .about-client{
    padding: .81em 1em .875em;
  }
}
