.footer{
  background-color: $black;
  color: #fff;
  position: relative;
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-top,
.footer-bottom{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.25em 0;
}

.footer-top{
  border-bottom: 1px solid rgba(#fff,.07);
  &__links{
    display: flex;
  }
  &__link{
    font-weight: 300;
    margin-right: 4em;
    span{
      font-size: 1.0625em;
      line-height: 165%;
    }
  }
}

.footer-socials{
  &__items{
    margin-left: .75em;
  }
  &__item{
    width: 2.5em;
    height: 2.5em;
    @include center;
    border-radius: 50%;
    img{
      max-width: .9375em;
      max-height: .9375em;
    }
    &:hover{
      background-color: rgba(#fff,.05);
    }
  }
}

.footer-middle{
  padding: 3.75em 0;
  display: flex;
  justify-content: space-between;
}

.spoiler{
  &:not(._active){
    .spoiler__hidden{
      height: 0;
    }
  }
  &__toggle{
    cursor: pointer;
  }
  &__hidden{
    overflow: hidden;
    transition: all .5s;
  }
}

.footer-nav{
  &__top{
    margin-bottom: 1.25em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 1em);
    color: rgba(#fff,.7);
    span{
      font-size: .9375em;
      line-height: 150%;
    }
  }
  @media screen and (min-width: 993px){
    .spoiler__hidden{
      height: auto !important;
    }
  }
}

.subscribe-form{
  margin-top: .75em;
  width: 20.5625em;
  position: relative;
  &__field{
    input{
      background-color: rgba(#fff,.05);
      border-color: transparent;
      color: #fff;
      padding-right: 8em;
      height: 3.75em;
      &::placeholder{
        color: rgba(255, 255, 255, 0.52);
      }
      &:focus{
        background-color: rgba(#fff,.025);
      }
    }
  }
  &__btn{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    padding: 0;
    width: 7.4375em;
  }
}

.footer-app{
  margin-top: 1.875em;
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.25em;
  }
  &__link{
    display: flex;
    align-items: center;
    @include lastM(0 0 1.25em);
    &:not(:hover){
      opacity: .7;
    }
    &:hover{
      color: #fff;
    }
    img{
      width: 1em;
      margin-right: .81em;
    }
    span{
      font-size: .9375em;
      line-height: 120%;
    }
  }
}

.footer-bottom{
  border-top: 1px solid rgba(#fff,.07);
  &__c{
    font-size: .875em;
    color: rgba(#fff,.7);
  }
  &__nav{
    display: flex;
  }
}

.footer-dropdown{
  position: relative;
  @include lastM(0 1.25em 0 0);
  &._open{
    .footer-dropdown__list{
      opacity: 1;
      pointer-events: unset;
      transform: translateY(0);
    }
  }
  &__toggle{
    display: flex;
    align-items: center;
    padding: 0 1.06em;
    height: 2.5em;
    border-radius: 0.25em;
    background-color: rgba(#fff,.05);
    font-weight: 500;
    color: rgba(#fff,.7);
    transition: all .3s;
    cursor: pointer;
    svg{
      opacity: .7;
      margin-left: .56em;
      width: .375em;
    }
    &:hover{
      color: #fff;
      svg{
        opacity: 1;
      }
    }
  }
  &__title{
    font-size: .875em;
  }
  &__list{
    position: absolute;
    bottom: 100%;
    right: 0;
    margin-bottom: .25em;
    background: #505050;
    width: 9em;
    border-radius: .25em;
    overflow: hidden;
    transition: all ease .3s;
    opacity: 0;
    pointer-events: none;
    transform: translateY(-.5em);
    padding: 1em 0;
    a{
      font-size: .9375em;
      min-height: 2.33em;
      padding: .25em 2em;
      display: flex;
      align-items: center;
      color: rgba(#fff,.7);
      &:hover,
      &._active{
        background-color: rgba(#fff,.05);
      }
    }
  }
}

.scroll-top{
  position: absolute;
  right: 1.25em;
  bottom: 1.25em;
  background: rgba(#fff,.05);
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  @include center;
  z-index: 3;
  transition: all .3s;
  &:hover{
    cursor: pointer;
    background: rgba(#fff,.25);
  }
}



@media screen and (max-width: 1540px){
  @media screen and (min-width: 577px){
    .footer-bottom{
      padding-right: 4.375em;
    }
    .scroll-top{
      right: 2.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer-top{
    padding: 1.5em 0 1em;
    flex-direction: column;
    &__links{
      flex-wrap: wrap;
      justify-content: center;
    }
    &__link{
      font-weight: 500;
      margin-right: 1.5em;
      span{
        font-size: .75em;
        line-height: 187%;
      }
    }
  }

  .footer-socials{
    margin-top: 1em;
    &__title{
      font-size: .75em;
    }
  }

  .footer-middle{
    padding: 1em 0;
    display: block;
  }

  .footer-nav{
    &__top{
      min-height: 1.75em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: .875em;
      &:after{
        content: '';
        flex-shrink: 0;
        width: .5em;
        height: .5em;
        background-image: url(../img/arrow-bottom.svg);
        @include bgImgSize(100%);
        transition: all .4s;
      }
      .text17{
        font-size: .9375em;
      }
    }
    &._active{
      .footer-nav__top:after{
        transform: rotate(-180deg);
      }
    }
    &__link{
      span{
        font-size: .75em;
      }
    }
  }

  .footer-subscribe{
    margin-top: 1.625em;
  }

  .subscribe-form{
    width: 100%;
    &__field{
      height: 3.125em;
      input{
        padding-right: 13.5em;
        padding-left: 1.66em;
        font-size: .75em;
        height: 100%;
        border-radius: .33em;
      }
    }
    &__btn{
      width: 9em;
      span{
        font-size: .75em;
      }
    }
  }

  .footer-app{
    margin-top: 1.31em;
    &__links{
      flex-direction: row;
    }
    &__link{
      @include lastM(0 5.5em 0 0);
    }
  }

  .footer-bottom{
    padding-top: .875em;
    padding-bottom: 1.5em;
  }

  .footer-dropdown{
    @include lastM(0 .625em 0 0);
  }

  .scroll-top{
    right: 1.25em;
    bottom: 1.5em;
  }
}


@media screen and (max-width: 576px){
  .footer-bottom{
    display: block;
    &__c{
      line-height: 200%;
    }
    &__nav{
      padding-right: 4.375em;
      justify-content: space-between;
      margin-top: .875em;
    }
  }
}


@media screen and (max-width: 427px){
  .footer-top{
    padding-bottom: 1.5em;
  }

  .footer-socials{
    width: 100%;
    justify-content: space-between;
    margin-top: 1.5em;
  }

  .footer-subscribe{
    margin-top: 0;
  }

  .subscribe-form{
    &__field{
      input{
        padding-right: 10em;
      }
    }
    &__btn{
      width: 6.8125em;
    }
  }

  .footer-bottom{
    padding-bottom: 1.125em;
    &__c{
      text-align: center;
    }
    &__nav{
      justify-content: unset;
    }
  }

  .scroll-top{
    right: .9375em;
    bottom: 1.125em;
  }
}
