@import "../vars.scss";
.breadcrumbs {
    padding: 1.75em 0;
    li{
        color: $gray;
        position: relative;
        display: inline;
        &:last-child {
            color: $black;
        }
        &:not(:first-child) {
            margin-left: 1em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                color: #D1D8DA;
                top:  .0625em;
                right: 100%;
                margin-right: .5em;
                font-size: .875em;
                line-height: 150%;
                @include center;
            }
        }
        a,span{
            font-size: .875em;
            line-height: 150%;
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1.25em 0;
        li{
            font-size: .8625em;
        }
    }
}
